import { KEY } from '../../utilities/keyCode.util';
class EaseProfilePictureController {
    /* @ngInject */
    constructor($scope, $element) {
        this._scope = $scope;
        this._element = $element;
        this.fileInputBox = this._element.find('input')[0];
        this.reader = new FileReader();
        this.newImageData = '';
        this.loadingClass = '';
        this.dragDrop();
        this._element[0].onkeydown = (event) => {
            event.keyCode === KEY.ENTER || event.keyCode === KEY.SPACE ? this.fileInputBox.click() : null;
        };
    }
    uploadImage() {
        this.fileInputBox.onclick = (event) => {
            this.fileInputBox.value = '';
            event.stopPropagation ? event.stopPropagation() : (event.cancelBubble = true);
        };
        this.fileInputBox.onchange = (event) => {
            this.readFile(event.currentTarget.files[0]);
        };
        this.fileInputBox.click();
    }
    readFile(file) {
        this.reader.onload = (evt) => {
            let newImageData = evt.target.result;
            if (newImageData) {
                this._scope.$apply(() => { this['newImageData'] = ''; });
                this._scope.$apply(() => { this['newImageData'] = newImageData; });
                this.loadingClass = '';
            }
        };
        if (file) {
            let type = file.type;
            let acceptTypes = ['image/png', 'image/jpeg', 'image/jpg', 'image/gif'];
            if (acceptTypes.indexOf(type) !== -1) {
                this.loadingClass = 'cropImageLoading';
                this.reader.readAsDataURL(file);
            }
        }
    }
    dragDrop() {
        window.addEventListener('dragover', (event) => {
            event.preventDefault();
        }, false);
        window.addEventListener('dragenter', (event) => {
            event.preventDefault();
        }, false);
        window.addEventListener('dragleave', (event) => {
            event.preventDefault();
        }, false);
        window.addEventListener('drop', (event) => {
            event.preventDefault();
        }, false);
        this._element[0].ondragover = (event) => {
            event.dataTransfer.dropEffect = 'copy';
            event.preventDefault();
        };
        this._element[0].ondrop = (event) => this.readFile(event.dataTransfer.files[0]);
    }
    hasImg() {
        return this.currentImageData.indexOf('data:') !== -1;
    }
    ariaMsg() {
        return this.hasImg() ? 'Replace Photo' : 'Add Photo';
    }
}
export default EaseProfilePictureController;



