import * as angular from 'angular';
import easeHeaderComponent from './easeHeader.component';
/**
 * @ngdoc module
 * @name easeUiHeader
 * @module easeUiHeader
 * @description
 * This component has been developed to facilitate reuse of header in a webpage.
 *
 * @usage
 * ```html
 *<ease-ui-header back-button-click-handler = "handleBtnClick()" back-button-aria="Test back button"></ease-ui-header>
 * ```
 * @param {string} imageTitle Header image title.
 * @param {string} headerLogoUrl Url for the header image.
 * @param {string} backButtonAria Aria label for back button.
 * @param {string} backButtonTitle Back button title.
 * @param {string} backButtonTemplateUrl Template url for back button.
 * @param {string} backButtonClickHandler callback function to be invoked when back button is clicked.
 *
 *
 * @example
 * <example name="easeHeaderExample" module="easeHeaderExample">
 *   <file name="index.html">
 *   <script src="script.js"></script>
 *       <div ng-controller="headerController" >
 *        <ease-ui-header back-button-click-handler = "handleBtnClick()"
 *           back-button-aria="Test back button">
 *         </ease-ui-header>
 *      </div>
 *   </file>
 *   <file name="script.js">
 *     angular.module('easeHeaderExample', ['easeUiHeader'])
 *    .controller('headerController', ['$scope', function($scope) {
 *       $scope.handleBtnClick = function () {
 *         alert("Header button dummy callback")
 *       }
 *     }])
 *   </file>
 * </example>
 *
 */
export default angular.module('easeUiHeader', [])
    .component('easeUiHeader', easeHeaderComponent);



