import * as angular from 'angular';
import { easeFilterBarComponent } from './easeFilterBar.component';
/**
 * @ngdoc module
 * @name easeFilterBar
 * @module easeFilterBar
 * @description
 * This component is used for showing the filter bar along with custom categories to get
 * Filtering UX on L2 page. The component accepts an array containing objects for each filter category (categories) and a
 * JSON object containing the static content (filterLabel, filterCaption) for the component that has either been fetched
 * from Nuxeo or is customized by the consumer (staticContent).
 *
 * Language Support
 *
 * Please note this section is only required for EASE consumers: all other consumers are not required to pass the
 * staticContent object (though they are welcome to use it to pass customized content to the component).
 * If the object is not passed, the component will fall back to default English content.
 *
 * The filterBar component has been built to handle receiving static content from Nuxeo, in order to support multiple languages.
 * This is done via the staticContent parameter, which contains the filterLabel and filterCaption keys. These keys should have
 * the static content values fetched from Nuxeo. Currently, the available files are in Nuxeo under the EASE/CORE folder, and
 * are named 'core_component_transactionfilter_en_US.properties' (English) and 'core_component_transactionfilter_es_US.properties' (Spanish).
 * It is the consumer's responsibility to pass the correct content, as the component will simply display whatever is passed in the
 * staticContent object.
 *
 * @usage
 * ```html
 *  <ease-filter-bar categories = "vm.categories" static-content="vm.contentObj">
 *      <search-box></search-box>
 *  </ease-filter-bar>
 * ```
 * When user selects an item from the category flyout, action associated with
 * the object will be fired. You will need to fire 'category-selected' event
 * when user is done selecting the category item.
 * ```js
 * // Event structure
 * new CustomEvent('category-selected', {
 *
 *   detail: {value: 'Statement Picker', index: 1}
 * })
 * // Index points to index of the item being interacted with in your categories array you are passing.
 * document.dispatchEvent(event)
 * ```
 * Also component fires reseting-category-filter when user resets or hits on cancel icon. detail object will contain
 * value indicating which filter was being reset and you can act accordingly based on the value.
 * ```js
 * // Event structure
 * new CustomEvent('reseting-category-filter', {
 *   detail: {
 *     value, index
 *   }
 * })
 * ```
 *
 * This component will accept an array of your filter categories.
 * Object should have following properties.
 * @param {String} key This will be used to identify the object independently
 * @param {String} value The value you want to render in the DOM. E.g Custom Date
 * @param {Array} options This array contains Option object with following properties
 * @param {String} key This will be used to uniquely identify flyout list item.
 * @param {String} value This will be used to render the lable on the DOM.
 * @param {String} hoverText This will be used to render the tooltip for the full name.
 * @param {Function} action This function will be invoked on click of this particular property. Say you want to
 * open a Date Range picker or Statement Picker.
 *
 * @example
 * <example name="ease-filter-bar" module="easeFilterBarExample">
 *   <file name="index.html">
 *     <div ng-controller="FilterManagerCtrl as vm" style="width: 100%; margin: 10px">
 *         <ease-filter-bar
 *           categories = "vm.categories"
 *           static-content = "vm.contentObj"
 *         >
 *         </ease-filter-bar>
 *     </div>
 *   </file>
 *   <file name="filterBarExample.js">
 *     angular.module('easeFilterBarExample', ['easeUIComponents'])
 *       .controller('FilterManagerCtrl', function ($scope) {
 *         var vm = this;
 *         // This object should contain the content fetched from Nuxeo
 *         vm.contentObj = {
 *           filterLabel: 'Filter',
 *           filterCaption: 'FILTER BY:'
 *         };
 *         var dispatchEvent = function (value, index) {
 *           var event = new CustomEvent('category-selected', {
 *             detail: {value: value, index: index}
 *           })
 *           document.dispatchEvent(event)
 *         }
 *         var dateCategory = {
 *           key: 'custom-date',
 *           value: 'Custom Date',
 *           options: [{
 *             key: 'statement-picker',
 *             value: 'Statement Picker',
 *             action: function () {
 *               var event = new CustomEvent('category-selected', {
 *                 detail: {value: 'Statement Picker', index: 0}
 *               })
 *               document.dispatchEvent(event)
 *             }
 *           }, {
 *             key: 'date-range-picker',
 *             value: 'Date Range',
 *             action: function () {
 *               var event = new CustomEvent('category-selected', {
 *                 detail: {value: 'Date Range', index: 0}
 *               })
 *               document.dispatchEvent(event)
 *             }
 *           }]
 *         };
 *         var category = {
 *           key: 'category',
 *           value: 'Categories',
 *           options: [{
 *             key: 'restaurant',
 *             value: 'Restaurant',
 *             action: function () {
 *               var event = new CustomEvent('category-selected', {
 *                 detail: {value: 'Restaurant', index: 1}
 *               })
 *               document.dispatchEvent(event)
 *             }
 *           }, {
 *             key: 'fees',
 *             value: 'Late Fees',
 *             action: function () {
 *               var event = new CustomEvent('category-selected', {
 *                 detail: {value: 'Late Fees', index: 1}
 *               })
 *               document.dispatchEvent(event)
 *             }
 *           }, {
 *             key: 'movies',
 *             value: 'Movies',
 *             action: function () {
 *               var event = new CustomEvent('category-selected', {
 *                 detail: {value: 'Movies', index: 1}
 *               })
 *               document.dispatchEvent(event)
 *             }
 *           }]
 *         };
 *
 *         var cards = {
 *           key: 'card',
 *           value: 'Cards',
 *           options: [{
 *             key: '7573',
 *             value: '...7573',
 *             hoverText: 'John Doe',
 *             action: function (item) {
 *               var event = new CustomEvent('category-selected', {
 *                 detail: {value: '...7573', index: 2}
 *               })
 *               document.dispatchEvent(event)
 *             }
 *           }, {
 *             key: '1353',
 *             value: '...1353',
 *             hoverText: 'Jane Doe',
 *             action: function (item) {
 *               var event = new CustomEvent('category-selected', {
 *                 detail: {value: '...1353', index: 2}
 *               })
 *               document.dispatchEvent(event)
 *             }
 *           }]
 *         };
 *         vm.categories = [dateCategory, category, cards];
 *         document.addEventListener('reseting-category-filter', function (evt) {
 *           console.log(evt.detail);
 *         })
 *       });
 *   </file>
 * </example>
 */
export default angular.module('easeFilterBar', [])
    .component('easeFilterBar', easeFilterBarComponent);



