import * as angular from 'angular';
import { focusDropdownEntry } from '../../utilities/focusListEl.util';
import { KEY } from '../../utilities/keyCode.util';
import { PROPERTIES } from '../../utilities/componentProperties.util';
const includes = require('array-includes');
import 'custom-event-polyfill/custom-event-polyfill';
const DEFAULT_GUTTER = 1;
export default (function () {
    let _scope;
    let _isOpen = false;
    let _activeIndex = 0;
    let filterFlyoutManager = {};
    function closeFlyouts(currentFlyout) {
        Object.keys(filterFlyoutManager)
            .filter(key => currentFlyout[key] !== key)
            .forEach(shouldNotBeOpenFlyer => {
            filterFlyoutManager[shouldNotBeOpenFlyer] = false;
        });
    }
    function adjustFlyer(outerWidth, top, left, index) {
        if (outerWidth >= 600) {
            top = top + DEFAULT_GUTTER;
            if (index > 1) {
                left = left - 120;
            }
            else {
                left = left - 90;
            }
            left = left > 0 ? left : 0;
        }
        return {
            left: `${left}px`,
            top: `${top}px`
        };
    }
    function focusFirstCategory(options) {
        let [firstCategoryItem] = options;
        let { key: id } = firstCategoryItem;
        focusFilter({ id });
    }
    function focusFilter({ id }) {
        setTimeout(() => {
            let element = document.getElementById(id);
            if (element)
                element.focus();
        }, 0);
    }
    return class {
        /* @ngInject */
        constructor($scope) {
            _isOpen = false;
            _scope = $scope;
            this.staticContent = this.staticContent || {};
            this.staticContent.filterLabel = this.staticContent.filterLabel || PROPERTIES.transactionfilter.filter.button;
            this.staticContent.filterCaption = (this.staticContent.filterCaption || PROPERTIES.transactionfilter.filter.label).toUpperCase();
            this.categories.forEach(({ key }) => {
                filterFlyoutManager[key] = false;
            });
            window.addEventListener('resize', () => {
                Object.keys(filterFlyoutManager)
                    .forEach(flyout => {
                    filterFlyoutManager[flyout] = false;
                });
                _scope.$evalAsync();
            });
            this.categories = this.categories.map(category => {
                return Object.assign({}, category, {
                    id: `${category.key}-menu-item`,
                    labelId: `${category.key}-flyer`,
                    defaultValue: `${category.value}`
                });
            });
            // Listen for the clicks and if user clicks outside, close it
            document.addEventListener('click', (evt) => {
                let [openFlyer] = Object.keys(filterFlyoutManager)
                    .filter(key => filterFlyoutManager[key]);
                if (!includes([`${openFlyer}-flyer`, `${openFlyer}-menu-item`], evt.target.id)) {
                    filterFlyoutManager[openFlyer] = false;
                    _scope.$evalAsync();
                }
            });
            document.addEventListener('category-selected', (e) => {
                let { detail: { index, value } } = e;
                Object.assign(this['categories'][index], {
                    value,
                    isActionType: true
                });
            });
        }
        get isOpen() {
            return _isOpen;
        }
        set isOpen(value) {
            _isOpen = value;
            if (value) {
                focusFilter(this.categories[0]);
            }
        }
        handleFlyer(event, category) {
            let { keyCode } = event;
            if (includes([13, 32], keyCode))
                this.toggleFilterFlyout(category);
        }
        toggleFilterFlyout(currentFilterFlyout) {
            let { key, options } = currentFilterFlyout;
            let isFlying = filterFlyoutManager[key];
            closeFlyouts(currentFilterFlyout);
            filterFlyoutManager[key] = !isFlying;
            _activeIndex = 0;
            focusFirstCategory(options);
        }
        canPickerRender({ key }) {
            return filterFlyoutManager[key];
        }
        isFlyerOpen({ key }) {
            let flyer = document.getElementById(key);
            return !!flyer;
        }
        handleKeyPress(evt, category) {
            let { keyCode } = evt;
            if (keyCode === 27)
                this.toggleFilterFlyout(category);
            if (includes([13, 27], keyCode))
                focusFilter(category);
            let listItems = angular.element(evt.target.parentElement).find('li');
            _activeIndex = _activeIndex > listItems.length - 1 ? 0 : _activeIndex;
            _activeIndex = focusDropdownEntry(keyCode, listItems, _activeIndex);
        }
        handleFilterKeypress(option, evt, $index) {
            const { keyCode } = evt;
            if (keyCode === KEY.TAB && !evt.shiftKey || keyCode === KEY.TAB && evt.shiftKey) {
                evt.stopPropagation();
                closeFlyouts(this.categories[$index]);
                return;
            }
            if (evt && includes([13, 32], evt.keyCode))
                this.delegateFilterAction(option);
        }
        delegateFilterAction(option) {
            const { action } = option;
            action.call(option);
        }
        resetHandler(index, evt) {
            if (evt.keyCode === 13) {
                this.resetFilter(index);
            }
        }
        resetFilter(index) {
            let { defaultValue: value } = this.categories[index];
            Object.assign(this.categories[index], {
                value,
                isActionType: false
            });
            document.dispatchEvent(new CustomEvent('reseting-category-filter', {
                detail: { value, index }
            }));
            this.toggleFilterFlyout(this.categories[index]);
            focusFilter(this.categories[index]);
        }
    };
}());



