import { calendarConfig } from './easeCalendar.config';
import calendarUtils from './easeCalendar.utils';
import * as angular from 'angular';
import 'moment';
const DEFAULT_CALENDAR_PERIOD = {
    min_date: new Date(-8640000000000000),
    max_date: new Date(8640000000000000)
};
const calendarOptions = ['customClass', 'isDateDisabled', 'format_day', 'format_day_header', 'format_day_title',
    'format_month', 'format_month_title', 'format_year', 'max_date', 'min_date', 'calendar_format', 'time_zone'];
let step = { months: 1 };
const navigationMap = {
    '1': 'next',
    '-1': 'prev'
};
function canNavigate(currentDate, minPeriod, maxPeriod) {
    let minDate = moment(new Date(minPeriod)).startOf('month');
    let current = moment(new Date(currentDate)).startOf('day');
    let maxDate = moment(new Date(maxPeriod)).startOf('day');
    return (minDate.isSameOrBefore(current.clone().startOf('month'))) && (current.isSameOrBefore(maxDate));
}
function getDayObject(date, month, index, selected) {
    // [SD][EWCORE-1600] Adding boolean property to track disable day and its decorator.
    const disabledState = this.isDisabled(date) ? {
        disabled: 'disable-day',
        isDayDisabled: true
    } : {
        disabled: '',
        isDayDisabled: false
    };
    return Object.assign({}, {
        // [TODO][SD] Clean this if we ever upgrade angular
        isSecondary: date.getMonth() !== month ? 'secondary-day' : '',
        uid: `ease-calendar-${date.getMonth()}-${date.getDate()}`,
        label: moment(date).format(this.format_day),
        selected,
        date
    }, disabledState);
}
function calculateNextDate(key, date) {
    switch (key) {
        case 'left':
            date = date.subtract(1, 'days');
            break;
        case 'up':
            date = date.subtract(7, 'days');
            break;
        case 'right':
            date = date.add(1, 'days');
            break;
        case 'down':
            date = date.add(7, 'days');
            break;
        case 'pageup':
            date = date.subtract(1, 'months');
            break;
        case 'pagedown':
            date = date.add(1, 'months');
            break;
        case 'home':
            date = date.startOf('month');
            break;
        case 'end':
            date = date.endOf('month');
            break;
    }
    return date;
}
export default class {
    /* @ngInject */
    constructor($scope, $element) {
        this._scope = $scope;
        this._element = $element;
        this.uniqueId = `ease-calendar-${this._scope.$id}-${Math.floor(Math.random() * 10000)}`;
        this.configureCalendar();
        try {
            this.configureCalendar();
        }
        catch (e) {
            console.error(e);
        }
        this._scope.$on('calendar-popup-open', () => {
            this._element.find('tbody')[0].focus();
        });
        this.init(this._element.controller('ngModel'));
    }
    init(ngModelCtrl_) {
        this.ngModelCtrl = ngModelCtrl_;
        if (this['calendarOptions']['starting_day']) {
            this.activeDate = new Date(this['calendarOptions']['starting_day']);
        }
        else if (this.ngModelCtrl.$modelValue) {
            this.activeDate = new Date(this.ngModelCtrl.$modelValue);
        }
        else {
            this.activeDate = new Date();
        }
        this.ngModelCtrl.$render = function render() {
            this.render();
        }.bind(this);
    }
    render() {
        if (this.ngModelCtrl.$viewValue) {
            let date = new Date(this.ngModelCtrl.$viewValue);
            if (date instanceof Date) {
                this.activeDate = date;
            }
            else {
                console.error('EaseCalendar component: "ng-model value must be a valid Date object"');
            }
        }
        this.updateCalendar();
    }
    updateCalendar() {
        let year = this.activeDate.getFullYear();
        let month = this.activeDate.getMonth();
        let firstDayOfMonth = new Date(this.activeDate);
        let model = this.ngModelCtrl.$viewValue ? new Date(this.ngModelCtrl.$viewValue) : null;
        firstDayOfMonth.setFullYear(year, month, 1);
        let numDisplayedFromPreviousMonth = firstDayOfMonth.getDay();
        let firstDate = new Date(firstDayOfMonth);
        if (numDisplayedFromPreviousMonth > 0) {
            firstDate.setDate(-numDisplayedFromPreviousMonth + 1);
        }
        let monthCalendar = calendarUtils.getAllDatesInMonth(firstDate)
            .map((date, index) => {
            let selected = model && calendarUtils.compare(date, model) === 0;
            let isToday = calendarUtils.compare(date, new Date()) === 0;
            let day = getDayObject.call(this, date, month, index, selected);
            if (this['calendarOptions']['customClass']) {
                let { cssClass, subLabel } = this['calendarOptions']['customClass']({ date, selected });
                if (subLabel === '') {
                    subLabel = '\u00A0';
                }
                Object.assign(day, { cssClass, subLabel });
            }
            else {
                Object.assign(day, { subLabel: '\u00A0' });
            }
            if (calendarUtils.compare(day.date, this.activeDate) === 0 &&
                !this.isDisabled(this.activeDate)) {
                this.activeDay = day;
            }
            // [SD] I know for sure when there is no label, it is going to be ' '. Hence safe to mark it TODAY.
            if (isToday && !selected && day['subLabel'].length === 1) {
                this['today'] = day;
                const todayLabel = angular.isUndefined(this.staticContent) ? 'TODAY' : this.staticContent.today_label;
                Object.assign(day, { subLabel: todayLabel });
            }
            if (selected && !day['isSecondary'] && !day['cssClass']) {
                this['selectedDay'] = Object.assign(day, { uid: 'currently-selected' });
            }
            if (day.isSecondary) {
                let uid = `ease-calendar-${day.date.getMonth()}-${day.date.getDate()}`;
                Object.assign(day, { uid, label: '', subLabel: '', cssClass: 'secondary-day' });
            }
            return day;
        });
        this.updateCalendarMonth(monthCalendar);
    }
    updateCalendarMonth(monthCalendar) {
        this['title'] = moment(new Date(this.activeDate)).format(this.format_day_title);
        this['date_rows'] = calendarUtils.split(monthCalendar, 7);
        this['labels'] = this.date_rows[0]
            .map(day => ({
            abbr: moment(new Date(day.date)).format('dd')[0],
            full: moment(new Date(day.date)).format('dddd')
        }));
        if (this.activeDay)
            this['activeDateId'] = this.activeDay.uid;
        // [SD] [EWCORE-1870] Update Carousel buttons.
        const prevMonth = moment(new Date(this.activeDate))
            .subtract(1, 'months')
            .startOf('month')
            .toDate();
        const nextMonth = moment(new Date(this.activeDate))
            .add(1, 'months')
            .startOf('month')
            .toDate();
        this.updateCarousel(prevMonth, ['-1']);
        this.updateCarousel(nextMonth, ['1']);
    }
    isDisabled(date) {
        let { isDateDisabled } = this['calendarOptions'];
        return this.min_date && calendarUtils.compare(date, this.min_date) < 0 ||
            this.max_date && calendarUtils.compare(date, this.max_date) > 0 ||
            (!!isDateDisabled && isDateDisabled(date));
    }
    handleKeyDown(key, evt) {
        const SKIP_COUNT = 14; // THIS MAGIC number was pre-decided by Product owner.
        let date = moment(new Date(this.activeDate));
        let skipCounter = 0;
        // [FIX] [SD]:- EWCORE-1602 Skip disabled date and move to next enabled date.
        do {
            skipCounter += 1;
            date = calculateNextDate(key, date);
            // ONLY SKIP 14 times and if dates are still disabled, move on to avoid infinite loop.
            if (skipCounter > SKIP_COUNT)
                return;
        } while (this.isDisabled(new Date(date)));
        this.updateCarousel(new Date(date.format()), Object.keys(navigationMap), () => {
            this.activeDate = new Date(date);
            this.updateCalendar();
        });
    }
    configureCalendar() {
        if (!this.calendarOptions) {
            throw new Error('Please give me calendar-options');
        }
        else {
            calendarOptions.forEach((key) => {
                switch (key) {
                    case 'format_day':
                    case 'calendar_format':
                    case 'format_day_title':
                        this[key] = this['calendarOptions'][key] || calendarConfig[key];
                        break;
                    case 'max_date':
                    case 'min_date':
                        this[key] = this['calendarOptions'][key];
                        this._scope.$watch(() => this['calendarOptions'][key], (newValue, oldValue) => {
                            let updatedDate = new Date(newValue);
                            if (moment(updatedDate).isValid()) {
                                this[key] = updatedDate;
                                this.updateCalendar();
                            }
                            else {
                                this[key] = DEFAULT_CALENDAR_PERIOD[key];
                            }
                        });
                        break;
                }
            });
        }
    }
    move(direction) {
        let year = this.activeDate.getFullYear() + direction * (step.years || 0);
        let month = this.activeDate.getMonth() + direction * (step.months || 0);
        let targetDate = new Date(new Date(this.activeDate).setFullYear(year, month, 1));
        this.updateCarousel(targetDate, [direction], () => {
            this.activeDate.setFullYear(year, month, 1);
            this.updateCalendar();
        });
    }
    updateCarousel(targetDate, directions, updateFn = undefined) {
        directions
            .forEach((direction) => {
            if (canNavigate(targetDate, this['min_date'], this['max_date'])) {
                if (updateFn) {
                    // Toggle nav bar only if updateFn is provided
                    this[navigationMap[-1 * direction]] = false;
                    updateFn();
                }
                else {
                    this[navigationMap[direction]] = false;
                }
            }
            else {
                this[navigationMap[direction]] = true;
            }
        });
    }
    select(day) {
        if (!(this.isDisabled(day['date']) || day['isSecondary'])) {
            let date_ = this.ngModelCtrl.$viewValue ? new Date(this.ngModelCtrl.$viewValue) : new Date(0, 0, 0, 0, 0, 0, 0);
            date_.setFullYear(day['date'].getFullYear(), day['date'].getMonth(), day['date'].getDate());
            this.ngModelCtrl.$setViewValue(date_);
            this.ngModelCtrl.$render();
        }
    }
    keydown(evt) {
        let key = calendarUtils.keys[evt.which];
        if (!key || evt.shiftKey || evt.altKey)
            return;
        evt.preventDefault();
        if (key === 'enter' || key === 'space') {
            if (this.isDisabled(this.activeDate)) {
                return;
            }
            else {
                this.select(this.activeDay);
            }
        }
        else {
            this.handleKeyDown(key, evt);
        }
    }
}



