import * as angular from 'angular';
import easeImageCropComponent from './easeImageCrop.component';
/**
 * @ngdoc module
 * @name easeImageCrop
 * @module easeImageCrop
 *
 * @description
 * This component is used for the image cropping on the profile preference page. It has two components/directives.
 * The easeImageCrop directive is for the image cropping area. Please give tabindex = "0" role="region" aria-describedby="imageCropGuide" for the keyboard accessibility. Also give role="application" to the partent.
 * The easeProfilePicture component is renderring the circle to display the final cropped result image and also to trigger the new image file selection.
 * @usage
 * ```html
 *  <ease-image-crop image="newImageData" result-image="currentImageData" data-ng-class = "loadingClass"></ease-image-crop>
 *  <ease-profile-picture new-image-data = "newImageData" current-image-data = "currentImageData" loading-class = "loadingClass"></ease-profile-picture>
 * ```
 * @param {string} image This is used in the easeImageCrop directive in order to receive the image to start the cropping.
 * @param {string} resultImage This is used in the easeImageCrop directive in order to give back the cropped result image.
 * @param {string} newImageData This is used in the easeProfilePicture component in order to give new selected image to the cropping area.
 * @param {string} currentImageData This is used in the easeProfilePicture component in order to display current image and also receive new cropped image.
 * @param {string} loadingClass This is to make the cropping area (easeImageCrop directive) dynamicallly show the image loading CSS effect.
 *
 * @example
 * <example name="ease-image-crop" module="cropImageExample">
 *   <file name="script.js">
 *     angular.module('cropImageExample', ['easeUIComponents'])
 *       .controller('Ctrl', ["$scope", function ($scope) {
 *         $scope.newImageData = ''
 *         $scope.currentImageData = 'profileIcon.png'
 *         $scope.loadingClass = ''
 *       }])
 *   </file>
 *   <file name="index.html">
 *     <script src="script.js"></script>
 *     <style type="text/css">
 *      .cropArea {
 *        width: 400px;
 *        height: 300px;
 *        margin: auto;
 *        top: 150px;
 *        position: absolute;
 *        left: 50px;
 *      }
 *      .profilePictureCircle {
 *        position: absolute;
 *        left: 50px;
 *      }
 *     </style>
 *     <div ng-controller="Ctrl">
 *      <div class="profilePictureCircle">
 *        <ease-profile-picture new-image-data = "newImageData" current-image-data = "currentImageData" loading-class = "loadingClass"></ease-profile-picture>
 *      </div>
 *      <div class="cropArea" role="application">
 *        <ease-image-crop tabindex = "0" role="region" aria-describedby="imageCropGuide" image="newImageData" result-image="currentImageData" data-ng-class = "loadingClass">
 *        </ease-image-crop>
 *      </div>
 *   </div>
 *   </file>
 * </example>
 */
export default angular.module('easeImageCrop', [])
    .directive('easeImageCrop', easeImageCropComponent);



