        Object.assign(config, options);
        config.tipMessage = tipMessage;
        config.id = id;
        config.cleanedPosition = getCleanedPosition(config.positionAuto, config.position);
        config.curPosition = config.cleanedPosition;
        config.positionClasses = getAllPositionClasses(config.cleanedPosition);
