        let anchorSize = anchorEl.getBoundingClientRect();
        let tooltipWidthDiff = Math.max(tooltipSize.width - anchorSize.width, 0);
        let tooltipHeightDiff = Math.max(tooltipSize.height - anchorSize.height, 0);
        let halfWidthDiff = Math.round(tooltipWidthDiff / 2);
        let halfHeightDiff = Math.round(tooltipHeightDiff / 2);
