import * as Accounting from 'accounting';
import * as Mustache from 'mustache';
import Partial from './easeAccountDetail.mustache';
import primaryPartial from './partials/easeAccountDetailPrimary.mustache';
import secondaryPartial from './partials/easeAccountDetailSecondary.mustache';
import alertsPartial from './partials/easeAccountDetailAlerts.mustache';
import navigationPartial from './partials/easeAccountDetailNavigation.mustache';
/**
 * @private
 * @function formatData
 *
 * returns formatted data model by running
 * datapointFormatter on all values.
 *
 * @param {Object}       model       scondaryAccountDetail data
 * @returns {Object}
 *
 */
const formatData = function (model) {
    let formattedModel = {};
    Object.keys(model).forEach(function (key) {
        formattedModel[key] = key === 'accountName' ? model[key] : datapointFormatter(model[key]);
    });
    if (formattedModel.secondary && formattedModel.secondary.val) {
        formattedModel.secondary = formatPrimaryBalance(formattedModel.secondary);
    }
    if (formattedModel.primary && formattedModel.primary.val) {
        formattedModel.primary = formatPrimaryBalance(formattedModel.primary);
    }
    return formattedModel;
};
/**
 * @private
 * @function formatPrimaryBalance
 *
 * formats primaryBalance to breakdown
 * currency, amount and cents.
 *
 * @param {Object}       balance       values from primaryContent.secondary
 * @returns {Object}
 *
 */
const formatPrimaryBalance = function (balance) {
    return {
        key: balance.key,
        val: balance.val,
        attrs: balance.attrs || [],
        elementType: balance.elementType,
        negative: getNegativeValueFromMoney(balance.val),
        currency: balance.noFormat ? '' : getCurrencyFromMoney(balance.val),
        amount: balance.noFormat ? balance.val : getAmountFromMoney(balance.val),
        cents: balance.noFormat ? '' : balance.val.slice(-2),
        mobileHidden: balance.mobileHidden || false
    };
};
/**
 * @private
 * @function getCurrencyFromMoney
 *
 * returns correct currency to be
 * displayed in primary row
 *
 * @param {String}       money       formatted money
 * @returns {String}
 *
 */
const getCurrencyFromMoney = function (money) {
    return money.indexOf('-') > -1 ? money.substring(1, 2) : money.substring(0, 1);
};
/**
 * @private
 * @function getAmountFromMoney
 *
 * returns correct amount value to be
 * displayed in primary row
 *
 * @param {String}       money       formatted money
 * @returns {String}
 *
 */
const getAmountFromMoney = function (money) {
    return money.indexOf('-') > -1 ? money.substring(2, money.length - 3) : money.substring(1, money.length - 3);
};
/**
 * @private
 * @function getNegativeValueFromMoney
 *
 * returns negative string if negative
 * money is passed
 *
 * @param {String}       money       formatted money
 * @returns {String}
 *
 */
const getNegativeValueFromMoney = function (money) {
    return money.indexOf('-') > -1 ? '-' : '';
};
/**
 * @private
 * @function datapointFormatter
 *
 * formats values based on user set format
 * and sets elementType based on attributes
 *
 * @param {Object|Object[]}       data       values from secondaryAccountDetail
 * @returns {Object|Object[]}
 *
 */
const datapointFormatter = function (data) {
    if (Array.isArray(data)) {
        return data.map(datapointFormatter);
    }
    else {
        return {
            val: data.format ? Accounting[`format${data.format}`](data.val) : data.val,
            key: data.key,
            noFormat: data.format === null || data.format === undefined,
            elementType: getElementType(data),
            attrs: data.attrs,
            mobileHidden: data.mobileHidden || false
        };
    }
};
/**
 * @private
 * @function hasLinkAttribute
 *
 * checks if attribute list is HTML standard href
 * or angular specific ui-sref and returns
 * true/false
 *
 * @param {Object}       attr      attribute object
 * @returns Boolean
 *
 */
const hasLinkAttribute = function (attr) {
    return (attr.key === 'ui-sref' || attr.key === 'href');
};
/**
 * @private
 * @function getElementType
 *
 * checks if elementType exists, if so
 * returns elementType, else determines
 * elementType from whether or not there
 * is a linkAttribute
 *
 * @param {Object}       data       data object of elementType and attrs
 * @returns String
 *
 */
const getElementType = function (data) {
    if (data.elementType) {
        return data.elementType;
    }
    else {
        return data.attrs && data.attrs.filter(hasLinkAttribute).length ? 'a' : 'div';
    }
};
/**
 * @private
 * @function allHidden
 *
 * used to check if all elements in secondary/tertiary content are hidden,
 * if so return true to hide entire content block, else return false and
 * display as normal
 *
 * note that this will only apply on the mobile/phone view
 *
 * @param {Object} contentArray object for secondary/tertiary content
 * @return Boolean
 *
 */
const allHidden = function (contentArray) {
    if (!contentArray) {
        return true;
    }
    for (let i = 0; i < contentArray.length; i++) {
        if (!contentArray[i].mobileHidden) {
            return false;
        }
    }
    return true;
};
class EaseAccountDetailController {
    /**
     * @public
     * @function constructor
     *
     * converts model to object if string,
     * formats model based on user's format preferences and
     * sets html as rendered mustache template based on model.
     *
     * @param {Object|String}       model       data model of secondaryAccountDetail
     *
     */
    constructor(model = '') {
        try {
            const dataModel = typeof (model) !== 'object' ? Object.freeze(JSON.parse(model)) : Object.freeze(model);
            this.data = {
                primaryContent: dataModel.primaryContent ? formatData(dataModel.primaryContent) : false,
                secondaryContent: dataModel.secondaryContent ? formatData(dataModel.secondaryContent) : false,
                accountAlerts: {
                    alerts: dataModel.alerts ? dataModel.alerts : false
                },
                navigationLinks: dataModel.navigationLinks ? dataModel.navigationLinks : false
            };
            if (this.data.secondaryContent) {
                let secondaryHidden = allHidden(this.data.secondaryContent.secondary);
                this.data.secondaryContent.primaryMobileExpand = secondaryHidden;
                this.data.secondaryContent.secondaryMobileHide = secondaryHidden;
                this.data.secondaryContent.tertiaryMobileHide = allHidden(this.data.secondaryContent.tertiary);
            }
            this.html = Mustache.render(Partial, this.data, {
                primaryPartial,
                secondaryPartial,
                alertsPartial,
                navigationPartial
            });
        }
        catch (e) {
            this.html = `easeAccountDetail encountered an error: ${e.message}`;
        }
    }
    getNewAlertHtml(newAlerts) {
        return Mustache.render(alertsPartial, {
            alerts: newAlerts
        });
    }
}
export default EaseAccountDetailController;



