import { EaseHeaderController as controller } from './easeHeader.controller';
let easeHeaderComponent = {
    restrict: 'E',
    bindings: {
        ImageTitle: '@',
        headerLogoUrl: '@',
        logoAriaLabel: '@',
        backButtonAria: '@',
        backButtonTitle: '@',
        backButtonTemplateUrl: '@',
        backButtonClickHandler: '&'
    },
    template: `
    <div class="header back-button-present" role="heading" >
      <div class="container">
        <div data-ng-include="vm.backButtonTemplateUrl"  class="backButton" ></div>
        <div class="logo-container" aria-label="{{vm.logoAriaLabel}}">
          <a class="logo" atdd-id="logo" tabindex="0" target="_blank">
            <img 
              src="{{vm.headerLogoUrl}}"
              alt={{vm.imageTitle}}
              title={{vm.imageTitle}}
              role="heading">
          </a>
        </div>
      </div>
    </div>
  `,
    controller,
    controllerAs: 'vm'
};
export default easeHeaderComponent;



