import * as angular from 'angular';
class EaseModalService {
    /* @ngInject */
    constructor($animate, $document, $compile, $controller, $rootScope, $q, $templateRequest) {
        this.$animate = $animate;
        this.$document = $document;
        this.$compile = $compile;
        this.$controller = $controller;
        this.$rootScope = $rootScope;
        this.$q = $q;
        this.$templateRequest = $templateRequest;
        this.body = angular.element(this.$document[0].body);
        this.pageContent = this.$document[0].getElementById('page-content');
    }
    _getTemplate(template, templateUrl) {
        let deferred = this.$q.defer();
        if (template) {
            deferred.resolve(template);
        }
        else if (templateUrl) {
            this.$templateRequest(templateUrl, true)
                .then(template => deferred.resolve(template), error => deferred.reject(error));
        }
        else {
            deferred.reject('No template or templateUrl');
        }
        return deferred.promise;
    }
    _appendChild(parent, child) {
        return this.$animate.enter(child, parent, null);
    }
    showModal(options) {
        let deferred = this.$q.defer();
        let { controller } = options;
        this._getTemplate(options.template, options.templateUrl).then((template) => {
            let modalScope = (options.scope || this.$rootScope).$new();
            let closeDeferred = this.$q.defer();
            let closedDeferred = this.$q.defer();
            let inputs = {
                $scope: modalScope,
                close: (result) => {
                    let ev = new CustomEvent('easeUiModalClose');
                    dispatchEvent(ev);
                    closeDeferred.resolve(result);
                    if (this.pageContent) {
                        this.pageContent.setAttribute('aria-hidden', 'false');
                    }
                    this.$animate.leave(modalElement).then(() => {
                        closedDeferred.resolve(result);
                        modalScope.$destroy();
                        inputs.close = null;
                        deferred = null;
                        closeDeferred = null;
                        modal = null;
                        inputs = null;
                        modalElement = null;
                        modalScope = null;
                    });
                }
            };
            if (options.inputs) {
                angular.extend(inputs, options.inputs);
            }
            let modalElement = this.$compile(template)(modalScope);
            inputs.$element = modalElement;
            if (!controller) {
                controller = function ($scope, close) {
                    $scope.close = close;
                };
                controller.$inject = ['$scope', 'close'];
            }
            let modalController = this.$controller(controller, inputs, false);
            this._appendChild(this.body, modalElement);
            if (this.pageContent) {
                this.pageContent.setAttribute('aria-hidden', 'true');
            }
            let modal = {
                controller: modalController,
                scope: modalScope,
                element: modalElement,
                close: closeDeferred.promise,
                closed: closedDeferred.promise
            };
            deferred.resolve(modal);
        }).then(null, error => deferred.reject(error));
        return deferred.promise;
    }
}
export default EaseModalService;



