import { KEY } from '../../../utilities/keyCode.util';
import { PROPERTIES } from '../../../utilities/componentProperties.util';
const isIphone = () => {
    return navigator.userAgent.indexOf('iPhone OS') > -1;
};
export default (ctrls, scope, element) => {
    let cursorPos, keyCode, oldLength, newLength;
    ctrls.showMemo = false;
    ctrls.currency = ctrls.currency || PROPERTIES.forminputs.amount.currency;
    ctrls.staticContent.placeholder = ctrls.staticContent.placeholder || ctrls.placeholder || PROPERTIES.forminputs.amount.hint;
    ctrls.guideText = ctrls.staticContent.placeholder;
    if (!isIphone())
        ctrls.type = 'tel';
    scope.$watch('vm.inputVal', (newVal) => {
        if (!newVal) {
            formatAmount();
        }
    });
    const countDecimals = (amount) => {
        return (amount.split('.')[1] || []).length;
    };
    // helper method to remove trailing zeroes that are added in the course of formatting the input amount
    const removeZeroes = (amount, numDecimals) => {
        switch (numDecimals) {
            case 0:
                return amount.slice(0, -2);
            case 1:
                return amount.slice(0, -1);
            case 2:
                return amount;
        }
    };
    // helper method to add cents and trailing zeroes when user leaves the field without explicitly adding them
    const appendCents = (amount) => {
        if (amount.indexOf('.') === -1) {
            return amount + '.00';
        }
        else {
            switch (countDecimals(amount)) {
                case 0:
                    return amount + '00';
                case 1:
                    return amount + '0';
                case 2:
                    return amount;
            }
        }
    };
    // helper method to help correctly format the cent values in the guide text while the user is typing
    const appendGuideCents = (amount, numDecimals) => {
        if (amount.indexOf('.') === -1) {
            return amount + '.00';
        }
        else {
            return (numDecimals > 0) ? amount : amount + '00';
        }
    };
    // helper method to correctly format the guide text
    const formatGuide = (numDecimals) => {
        if (ctrls.inputContent.value) {
            ctrls.guideText = appendGuideCents(ctrls.inputContent.value, numDecimals);
        }
        else {
            ctrls.guideText = ctrls.staticContent.placeholder;
        }
    };
    const formatAmount = () => {
        cursorPos = ctrls.inputContent.selectionStart;
        let amount = ctrls.inputContent.value.replace(/[^0-9.]/g, '');
        if (amount === '0') {
            amount = '';
            cursorPos = 0;
        }
        else if (amount === '.') {
            amount = '0.';
            cursorPos = 2;
        }
        let decimalIndex = amount.indexOf('.');
        let numDecimals = countDecimals(amount);
        if (numDecimals > 2) {
            amount = amount.substring(0, decimalIndex) + amount.substring(decimalIndex, decimalIndex + 3);
            numDecimals = 2;
        }
        amount = parseFloat(amount).toFixed(2); // convert input to float and back, will remove leading zeroes and change input to XXX.XX format
        if (decimalIndex === -1) {
            amount = amount.replace(/(\d)(?=(\d{3})+\.)/g, '$1,').slice(0, -3);
        }
        else {
            amount = removeZeroes(amount.replace(/(\d)(?=(\d{3})+\.)/g, '$1,'), numDecimals);
        }
        newLength = amount.length;
        ctrls.inputContent.value = amount;
        ctrls.inputVal = ctrls.inputContent.value;
        formatGuide(numDecimals);
        if (newLength === oldLength) {
            cursorPos = cursorPos - 1;
        }
        if (newLength - oldLength === 2) {
            cursorPos = cursorPos + 1;
        }
        else if ((keyCode === KEY.BACKSPACE || keyCode === KEY.DELETE) && oldLength - newLength === 2) {
            cursorPos = cursorPos - 1;
        }
        oldLength = newLength;
        ctrls.timeout(() => { ctrls.inputContent.setSelectionRange(cursorPos, cursorPos); });
    };
    ctrls.displayMemo = () => {
        ctrls.showMemo = true;
    };
    ctrls.onInput = () => {
        ctrls.hasError = false;
        ctrls.errorOutput = '';
        ctrls.inputChanged = true;
        ctrls.timeout(() => {
            formatAmount();
        });
    };
    ctrls.onFocus = () => {
        oldLength = ctrls.inputContent.value.length;
        ctrls.inputChanged = false;
        ctrls.inFocus = true;
        ctrls.timeout(() => { });
        ctrls.isBlur = false;
    };
    ctrls.onBlur = (e) => {
        ctrls.isBlur = true;
        if (ctrls.inputContent.value) {
            ctrls.inputContent.value = appendCents(ctrls.inputContent.value); // add decimal point and/or cent values if not explicitly entered by user
            ctrls.inputVal = ctrls.inputContent.value;
        }
        ctrls.checkContent(e);
    };
    ctrls.onKeydown = (e) => {
        keyCode = e.which;
        keyCode === KEY.SPACE ? e.preventDefault() : null;
        if (keyCode === KEY.BACKSPACE && ctrls.inputContent.value[ctrls.inputContent.selectionStart - 1] === ',') {
            // if character directly before cursor is a comma and user hits backspace key, need to move cursor so number directly before the comma is deleted
            ctrls.inputContent.setSelectionRange(ctrls.inputContent.selectionStart - 1, ctrls.inputContent.selectionStart - 1);
        }
        else if (keyCode === KEY.DELETE && ctrls.inputContent.value[ctrls.inputContent.selectionStart] === ',') {
            // if character directly after cursor is a comma and user hits delete key, need to move cursor so number directly after the comma is deleted
            ctrls.inputContent.setSelectionRange(ctrls.inputContent.selectionStart + 1, ctrls.inputContent.selectionStart + 1);
        }
        ctrls.handleKeydown(e);
    };
    if (ctrls.inputVal) {
        ctrls.inputContent.value = ctrls.inputVal;
        formatAmount();
    }
};



