import controller from './easeQuarterlyPicker.controller';
import 'ease-quarterly-picker.scss';
let easeQuarterlyPickerComponent = {
    restrict: 'E',
    bindings: {
        quarters: '=',
        currentQuarter: '='
    },
    template: `
    <div class="ease-quarter-picker">
      <ul class="ease-quarter-display {{'length-' + vm.quarters.length}}">
        <li class="ease-quarter" data-ng-repeat="quarter in vm.quarters" data-ng-show="quarter.available">
          <button class="ease-quarter-bubble" data-ng-class="{'active': (quarter.title === vm.currentQuarter.title && quarter.year === vm.currentQuarter.year)}" aria-label="Quarterly statement for {{quarter.title.substr(0,7)}}" id="{{quarter.title.substr(0,7)}}-bubble" data-ng-click="vm.selectQuarter(quarter)">
            <b>{{quarter.title.substr(0,7)}}</b><br>{{quarter.title.substr(7)}}
          </button>
        </li>
      </ul>
    </div>`,
    controller,
    controllerAs: 'vm'
};
export default easeQuarterlyPickerComponent;



