import inputEmail from './easeInputEmail';
import inputPhone from './easeInputPhone';
import inputDate from './easeInputDate';
import inputAmount from './easeInputAmount';
let inputLibrary = {
    'email': inputEmail,
    'phone': inputPhone,
    'date': inputDate,
    'amount': inputAmount
};
export default (type, ctrls, scope, element) => {
    inputLibrary[type] ? inputLibrary[type](ctrls, scope, element) : null;
};



