import controller from './easeImageCrop.controller';
import 'ease-crop-image.scss';
export default () => {
    return {
        restrict: 'E',
        scope: {
            image: '=',
            resultImage: '='
        },
        bindToController: true,
        template: `
    <div>
      <span class="offscreen" id="imageCropGuide">{{vm.cropA11yMsg}}</span>
      <canvas aria-label = "{{vm.canvasAriaMsg}}"></canvas>
    </div>`,
        controller,
        controllerAs: 'vm'
    };
};



