import * as angular from 'angular';
import easeTooltipComponent from './easeTooltip.component';
/**
 * @ngdoc module
 * @name easeTooltip
 * @module easeTooltip
 * @description
 * Shows a tooltip when inserted into the UI. The tooltip message displayed to the user is controlled by what is passed into the 'tipMessage' variable (the only required param).
 *
 * Additional attributes can be passed in an options object, like triggerMessage, position, positionAuto, size, and showIcon.
 *
 * You can also pass in custom attrs as an array of {key: 'attr', value: 'attrValue'} pairs.
 *
 * Additional details on parameters are detailed below.
 *
 * FAQ:
 * -----------
 * How does the positioning work?
 *
 * Position can be passed in as a string or an object. In conjunction with positionAuto, there are 4 possibilities:
 *
 * (1) position: string && positionAuto: false - tooltip is statically at that position
 *
 * (2) position: object && positionAuto: false - tooltip is statically at each position defined in the position object per breakpoint key. The allowed keys are 'desktop', 'tablet', and 'phone'.
 *
 * For example, given a position object {desktop: 'right', tablet: 'top', phone: 'bottom'}, the tooltip will show to the right at desktop breakpoint, top on tablet, and bottom on phone.
 *
 * If a given breakpoint is not defined (i.e. if 'phone' were missing in the example), the tooltip will use the default position ('top') at that breakpoint.
 *
 * (3) position: string && positionAuto: true - tooltip will test if the given position is visible in the current viewport and if so, will use that position. If not, will try alternate positions as described below.
 *
 * (4) position: object && positionAuto: true - positionAuto isn't meant to be used with a position object. In this case tooltip will convert position to a string equal to the first position value in the object, and then revert to case (3).
 *
 * =============================================================
 *
 * How does positionAuto work?
 *
 * positionAuto tries the given position, and then tries alternate positions in this order:
 *
 * (1) If tooltip's primary position is along the 'top' or 'bottom':
 *
 * [position], [position]-left, [position]-right, [opposite side], [opposite site]-left, [opposite side]-right, left, left-top, left-bottom, right, right-top, right-bottom
 *
 * (2) If tooltip's primary position is along the 'left' or 'right':
 *
 * [position], [position]-top, [position]-bottom, [opposite side], [opposite site]-top, [opposite side]-bottom, top, top-left, top-right, bottom, bottom-left, bottom-right
 *
 * After trying all positions, if it still hasn't found 1 that is visible in the current viewport, it will just display the tooltip at the given position.
 *
 * =============================================================
 *
 * Can showIcon be false and triggerMessage be empty at the same time?
 *
 * Technically yes, but then the user won't have anything to hover/click on so doesn't seem advisable. However the tooltip will still appear in the tab order.
 *
 * =============================================================
 *
 * How does behavior differ between desktop and tablet/phone?
 *
 * On desktop, tooltip visibility is triggered by hover and tab. On tablet/phone, it is triggered on tap. Tapping multiple times toggles visibility; tapping away hides the tooltip as well.
 *
 * =============================================================
 *
 * Can I have a link in the tooltip?
 *
 * That's inadvisable as right now elements in the tooltip would not be keyboard-only accessible (through tabbing). If you choose to implement this, you are in charge of making the content properly accessible.
 *
 * =============================================================
 *
 * Can I pass in a custom ID/function/etc (i.e. for TeaLeaf or SiteCatalyst)?
 *
 * Yes, custom attributes are supported. See the "custom attributes" example below.
 *
 * =============================================================
 *
 * @usage
 * ```html
 * // default options: would generate an info icon that has the message on hover
 * <ease-tooltip tip-message="'Your tooltip message goes here'">
 * </ease-tooltip>
 * ```
 *
 * @param {string} tipMessage Message to be displayed in the tooltip. Can contain code.
 * @param {object} [options={attrs: (empty array), position: top, positionAuto: false, size: 'large', showIcon: true, triggerMessage: ''}] Options hash with several expected values. More details on each below.
 * @param {array} [options.attrs=(empty array)] Attrs is an array catch-all for other attributes (formatted as {key: 'attr', value: 'attrValue'}) you want to pass in, which will get attached to a container div.
 * @param {string/object} [options.position='top'] Can be a string or an object. Available positions are: 'top', 'top-left', 'top-right', 'left', 'left-top', 'left-bottom', 'bottom', 'bottom-left', 'bottom-right', 'right', 'right-top', 'right-bottom'. If an object, can pass in up to 3 positions in {breakpoint: position} format. More info in the FAQ.
 * @param {boolean} [options.positionAuto=false] If true, tests if the stated position (if position object passed in, just uses the first position value) is in the viewport, otherwise tries alternate positions, in an order detailed in the FAQ.
 * @param {string} [options.size='large'] Size modifiers for the tooltip. 'large' is 300px, 'medium' is 200px, and 'small' is 100px.
 * @param {boolean} [options.showIcon=true] If true, shows the info icon and anchors the tooltip carat to it. If false, doesn't show the icon and centers the carat on the triggerMessage.
 * @param {string} [options.triggerMessage=''] Text on the page that will also display the tooltip on hover (or click on mobile/tablet). Can contain code.
 *
 * @example
 * <example name="easeTooltip" module="easeTooltipDemo">
 *   <file name="index.html">
 *     <div class="wrapper" ng-controller="easeTooltipDemoCtrl as vm">
 *      <h3>Auto-positioned Tooltips</h3>
 *      <p>
 *         <ease-tooltip
 *           tip-message="'Your account number is the 16-digit number found on the front of your credit card.'"
 *         options="vm.auto1obj"
 *       ></ease-tooltip>
 *     </p>
 *     <p>
 *       <ease-tooltip
 *         tip-message="'Your account number is the 16-digit number found on the front of your credit card.'"
 *         options="vm.auto2obj"
 *       ></ease-tooltip>
 *     </p>
 *     <h3>Non-Auto-Position Tooltips</h3>
 *     <p>
 *       <ease-tooltip
 *         tip-message="'Your account number is the 16-digit number found on the front of your credit card.'"
 *         options="vm.nonauto1obj"
 *       ></ease-tooltip>
 *     </p>
 *     <p>
 *       <ease-tooltip
 *         tip-message="'Your account number is the 16-digit number found on the front of your credit card.'"
 *         options="vm.nonauto2obj"
 *       ></ease-tooltip>
 *     </p>
 *     <h3>Non-Auto-Position, Breakpoint-specified Tooltips</h3>
 *     <p>
 *       <ease-tooltip
 *         tip-message="'Your account number is the 16-digit number found on the front of your credit card.'"
 *         options="vm.posobj1obj"
 *       ></ease-tooltip>
 *     </p>
 *     <p>
 *       <ease-tooltip
 *         tip-message="'Your account number is the 16-digit number found on the front of your credit card.'"
 *         options="vm.posobj2obj"
 *       ></ease-tooltip>
 *     </p>
 *     <h3>Tooltip Sizes</h3>
 *     <p>
 *       <ease-tooltip
 *         tip-message="'Your account number'"
 *         options="vm.smallObj"
 *       ></ease-tooltip>
 *     </p>
 *     <p>
 *       <ease-tooltip
 *         tip-message="'Your account number is on the front of your credit card.'"
 *         options="vm.mediumObj"
 *       ></ease-tooltip>
 *     </p>
 *     <p>
 *       <ease-tooltip
 *         tip-message="'Your account number is the 16-digit number found on the front of your credit card.'"
 *         options="vm.largeObj"
 *       ></ease-tooltip>
 *     </p>
 *     <h3>Icon-only Tooltips</h3>
 *     <p align="center">
 *       <ease-tooltip
 *         tip-message="'This tooltip is 100% default options'"
 *       ></ease-tooltip>
 *     </p>
 *     <p align="center">
 *       <ease-tooltip
 *         tip-message="'This tooltip is top-right, medium size, positionAuto default (false)'"
 *         options="vm.iconOnlyObj"
 *       ></ease-tooltip>
 *     </p>
 *     <h3>Custom Attributes</h3>
 *     <p>
 *       <ease-tooltip
 *         tip-message="'I have custom attributes.'"
 *         options="vm.customAttrs"
 *       ></ease-tooltip>
 *     </p>
 *     <h3>Modal Example</h3>
 *     <button data-ng-click="vm.openStandardModal()">Open Modal</button><br>
 *    </div>
 *   </file>
 *   <file name="script.js">
 *    angular.module('easeTooltipDemo', ['easeUIComponents'])
 *    .controller('easeTooltipDemoCtrl', ['easeUIModalService', function (easeUIModalService) {
 *     this.auto1obj = {
 *       position: 'top',
 *       positionAuto: true,
 *       triggerMessage: 'position: top, show-icon: default (true), positionAuto: true'
 *     };
 *     this.auto2obj = {
 *       position: 'right',
 *       positionAuto: true,
 *       showIcon: false,
 *       triggerMessage: 'position: right, show-icon: false, positionAuto: true'
 *     };
 *     this.nonauto1obj = {
 *       position: 'top',
 *       triggerMessage: 'position: top, show-icon: default (true), positionAuto: default (false)'
 *     };
 *     this.nonauto2obj = {
 *       position: 'left',
 *       showIcon: false,
 *       triggerMessage: 'position: left, show-icon: false, positionAuto: default (false)'
 *     };
 *     this.posobj1obj = {
 *       position: {
 *         desktop: 'right',
 *         tablet: 'bottom-right',
 *         phone: 'bottom'
 *       },
 *       triggerMessage: 'position: D: right, T: bottom-right, P: bottom, show-icon: default (true), positionAuto: default (false)'
 *     };
 *     this.posobj2obj = {
 *       position: {
 *         desktop: 'bottom',
 *         tablet: 'bottom-left'
 *       },
 *       showIcon: false,
 *       triggerMessage: 'position: D: bottom, T: bottom-left, P: default (top), show-icon: false, positionAuto: default (false)'
 *     };
 *     this.smallObj = {
 *       position: 'top-left',
 *       positionAuto: true,
 *       size: 'small',
 *       triggerMessage: 'position: top-left, show-icon: default (true), size: small, positionAuto: true'
 *     };
 *     this.mediumObj = {
 *       position: 'top-left',
 *       positionAuto: true,
 *       size: 'medium',
 *       triggerMessage: 'position: top-left, show-icon: default (true), size: medium, positionAuto: true'
 *     };
 *     this.largeObj = {
 *       position: 'top-left',
 *       positionAuto: true,
 *       size: 'large',
 *       triggerMessage: 'position: top-left, show-icon: default (true), size: large (default), positionAuto: true'
 *     };
 *     this.iconOnlyObj = {
 *       position: 'top-right',
 *       size: 'medium'
 *     };
 *     this.customAttrs = {
 *       attrs: [
 *         {key: 'class', value: 'custom-class'},
 *         {key: 'id', value: 'custom-id'},
 *         {key: 'ng-click', value: 'vm.alertFn()'}
 *       ],
 *       position: 'top-left',
 *       size: 'medium',
 *       triggerMessage: 'I have some custom attrs--a class, an ID, and a click fn. Could be useful for SiteCatalyst, TeaLeaf, etc.'
 *     };
 *     this.alertFn = function () {
 *       alert('This is an example of a custom function passed in through the custom attributes object. Instead of a popup box, this may be used for SiteCatalyst tracking functions, for example.')
 *     }
 *     this.openStandardModal = function() {
 *      easeUIModalService.showModal({
 *        templateUrl: 'standardModal.html',
 *        controller: 'StandardModalController'
 *      }).then(function(modal) {
 *        modal.close.then(function(result) {})
 *      })
 *    }
 *   }])
 *   .controller('StandardModalController', ['$scope', 'close', function($scope, close) {
 *     $scope.modalMessage = 'Your modal content will appear here.'
 *     $scope.close = close
 *     $scope.auto1obj = {
 *       position: 'top',
 *       positionAuto: true,
 *       triggerMessage: 'position: top, show-icon: default (true), positionAuto: true'
 *       };
 *    }])
 *   </file>
 *   <file name="standardModal.html">
 *    <ease-ui-modal close-modal="close()" modal-id="'standard'" modal-title="'Lorem ipsum'">
 *      <div style="text-align: center;" tabindex="0" aria-label="">  </div>
 *      <p>
 *        <ease-tooltip
 *          tip-message="'Your account number is the 16-digit number found on the front of your credit card.'"
 *          options="auto1obj"
 *        ></ease-tooltip>
 *      </p>
 *    </ease-ui-modal>
 *   </file>
 *   <file name="style.css">
 *       .wrapper {
 *         margin: 0px;
 *       }
 *       .custom-class {
 *         border: 1px solid red;
 *       }
 *      #custom-id {
 *        background-color: yellow;
 *      }
 *   </file>
 * </example>
 */
export default angular.module('easeTooltip', [])
    .component('easeTooltip', easeTooltipComponent);



