const moment = require('moment');
const matchSearchInput = (value, searchInput, property) => {
    let beforeDollarSign;
    let afterDollarSign;
    if (property.toLowerCase().indexOf('date') !== -1) {
        let date = new Date(value);
        let mmdyyyy = moment(date).format('MM/D/YYYY'); // mm/d/yyyy string, no leading zero for dates 1-9, i.e. 04/1/2016
        let mmddyyyy = moment(date).format('MM/DD/YYYY'); // mm/dd/yyyy string, leading zero for dates 1-9, i.e. 04/01/2016
        let mmmdyyyy = moment(date).format('MMM D, YYYY').toLowerCase(); // month's first three letters, no leading zero for dates 1-9, i.e. Apr 1, 2016
        let mmmddyyyy = moment(date).format('MMM DD, YYYY').toLowerCase(); // month's first three letters with leading zero for dates 1-9, i.e. Apr 01, 2016
        let mmmmdyyyy = moment(date).format('MMMM D, YYYY').toLowerCase(); // full month name with no leading zero for dates 1-9, i.e. April 1, 2016
        let mmmmddyyyy = moment(date).format('MMMM DD, YYYY').toLowerCase(); // full month name with leading zero for dates 1-9, i.e. April 01, 2016
        return value && (mmdyyyy.indexOf(searchInput) !== -1 || mmddyyyy.indexOf(searchInput) !== -1 || mmmdyyyy.indexOf(searchInput) !== -1 ||
            mmmddyyyy.indexOf(searchInput) !== -1 || mmmmdyyyy.indexOf(searchInput) !== -1 || mmmmddyyyy.indexOf(searchInput) !== -1);
    }
    else if (property.toLowerCase().indexOf('amount') !== -1 || property.toLowerCase().indexOf('balance') !== -1) {
        if (typeof value === 'number') {
            let moneyVal = Math.abs(value).toFixed(2);
            beforeDollarSign = '-$' + moneyVal; // need to be able to match negative sign both before and after dollar sign
            afterDollarSign = '$-' + moneyVal;
        }
        else {
            beforeDollarSign = '-$' + value;
            afterDollarSign = '$-' + value;
        }
        return value && (beforeDollarSign.indexOf(searchInput) !== -1 || afterDollarSign.indexOf(searchInput) !== -1);
    }
    else {
        return value && value.toString().toLowerCase().indexOf(searchInput) !== -1;
    }
};
// method to get value of field, handles cases where field is nested
const getElementVal = (element, property) => {
    let elementVal = element;
    let propertyFields = property.split('.');
    for (let i = 0; i < propertyFields.length; i++) {
        elementVal = elementVal[propertyFields[i]];
        if (!elementVal) {
            return null;
        }
    }
    return elementVal;
};
function easeSearchBarFilter() {
    return (items = [], searchText = '', properties = [], dateRange = undefined) => {
        let output = items;
        if (dateRange && dateRange.start && dateRange.end) {
            output = output.filter((element) => {
                return properties.some((property) => {
                    if (property.indexOf('date') === -1) {
                        return false;
                    }
                    let elementVal = getElementVal(element, property);
                    if (!elementVal) {
                        return false;
                    }
                    let date = new Date(elementVal);
                    return moment(date).isBetween(dateRange.start, dateRange.end, null, '[]'); // inclusive check for date range
                });
            });
        }
        output = output.filter((element) => {
            let searchInput = searchText.toLowerCase();
            let returnValue = properties.some((property) => {
                let elementVal = getElementVal(element, property);
                if (!elementVal) {
                    return false;
                }
                return matchSearchInput(elementVal, searchInput, property);
            });
            return returnValue;
        });
        return output;
    };
}
export default easeSearchBarFilter;



