import 'ease-L3header.scss';
import BACK_BTN_TEMPLATE from './easeBackButton.html';
export const CAPITAL_ONE_LOGO = require('capital_one_logo.svg');
export const CAPITAL_ONE_IMAGE_TITLE = 'Capital One';
export const BACK_BTN_TITLE = 'Back';
export const BACK_BTN_ARIA_LABEL = 'Back Button';
export const LOGO_ARIA = 'Capital One';
export class EaseHeaderController {
    constructor() {
        this.name = 'easeL3Page';
        this.init();
    }
    init() {
        this.populateValues(this.headerLogoUrl, this.imageTitle, this.backButtonTemplateUrl, this.backButtonTitle, this.backButtonAria, this.logoAriaLabel);
    }
    populateValues(headerLogoUrl = CAPITAL_ONE_LOGO, imageTitle = CAPITAL_ONE_IMAGE_TITLE, backButtonTemplateUrl = BACK_BTN_TEMPLATE, backButtonTitle = BACK_BTN_TITLE, backButtonAria = BACK_BTN_ARIA_LABEL, logoAriaLabel = LOGO_ARIA) {
        this.headerLogoUrl = headerLogoUrl;
        this.imageTitle = imageTitle;
        this.backButtonTemplateUrl = backButtonTemplateUrl;
        this.backButtonTitle = backButtonTitle;
        this.backButtonAria = backButtonAria;
        this.logoAriaLabel = logoAriaLabel;
    }
    handleBackButtonClick() {
        return this.backButtonClickHandler();
    }
}



