export default class {
    selectMonth(month) {
        // [SD][TODO] What happens when not available?
        if (month.available) {
            this.currentMonth = month;
        }
    }
    isAvailable(available) {
        // [SD] [TODO] Why is this not localized?
        return available ? 'available' : 'not available';
    }
    isActive(month, current) {
        return month === current;
    }
}



