import * as angular from 'angular';
const transcludeFactory = () => {
    let hasHeader;
    return {
        transclude(elem, transcludeFn) {
            transcludeFn((clone) => {
                const identifiers = ['HEADER-PART', 'FIRST-PART', 'SECOND-PART'];
                // [TODO][ZL] Better to use Array.from but it breaks in phantom
                let elementsArray = [].slice.call(clone)
                    .filter((cloneEl) => {
                    return identifiers.indexOf(cloneEl.tagName) !== -1;
                });
                if (elementsArray[0].tagName === 'HEADER-PART') {
                    hasHeader = true;
                    angular.element(elem.find('header')[0]).append(elementsArray.shift());
                    angular.element(elem.find('aside')[0]).append(elementsArray.shift());
                    angular.element(elem.find('aside')[1]).append(elementsArray.shift());
                }
                else {
                    hasHeader = false;
                    angular.element(elem.find('aside')[0]).append(elementsArray.shift());
                    angular.element(elem.find('aside')[1]).append(elementsArray.shift());
                }
            });
        },
        getHeader() {
            return hasHeader;
        }
    };
};
export default transcludeFactory;



