import inputConfig from './types';
/* @ngInject */
export default ($provide, $injector) => {
    const easeInputTypeDelegate = ($delegate) => {
        let directive = $delegate[0];
        directive.compile = () => {
            return (scope, element, attrs, ctrls) => {
                if (!attrs.inputType)
                    return;
                inputConfig(attrs.inputType.toLowerCase(), ctrls, scope, element);
            };
        };
        return $delegate;
    };
    $injector.has('easeInputDirective') ? $provide.decorator('easeInputDirective', ['$delegate', easeInputTypeDelegate]) : null;
};



