import * as angular from 'angular';
import easeErrorMessageComponent from './easeErrorMessage.component';
/**
 * @ngdoc module
 * @name easeErrorMessage
 * @module easeErrorMessage
 * @description
 * erroMessage component will be used as a placeholder to display error messages in the webpage.
 *
 * @usage
 * ```html
 *<ease-error-message error-options="options" callback="callback"></ease-error-message>
 * ```
 *
 * @param {object} [errorOptions={attrs: iconColor: ``, alertContent: ``, isCloseIcon: true/false, globalElementId: ``, alertIcon: true/false, closeAlert: trur/false, alertIconAria: ``, closeIconAria: ``}] Options hash with several expected values. More details on each below.
 * @param {string} iconColor color of the alert icon.
 * @param {string} alertContent content for the alert message.
 * @param {boolean} isCloseIcon property to display the close icon
 * @param {boolean} closeAlert property to open and close the error message
 * @param {boolean} alertIcon property to display alert icon
 * @param {string} globalElementId property to generate the container id
 * @param {string} alertIconAria property to add the Icon text
 * @param {string} closeIconAria property to add the close icon text
 * @param {function} callback Optional property callback function, check the below implementation
 * @example
 * <example name="easeErrorMessage" module="easeErrorMessageExample">
 *   <file name="index.html">
 *
 *   <div ng-controller="errorController">
 *        <h4>easeErrorMessage component with alert icon</h4>
 *        <ease-error-message  error-options="withOutClose"></ease-error-message>
 *        <h4>easeErrorMessage component with 'X'</h4>
 *        <ease-error-message error-options="withClose"></ease-error-message>
 *        <h4>easeErrorMessage component with alert icon and 'X'</h4>
 *        <ease-error-message error-options="withAlertAndClose" callback="callback"></ease-error-message>
 *        <h4>easeErrorMessage component without alert icon and 'X'</h4>
 *        <ease-error-message error-options="withOutAlertClose"></ease-error-message>
 *        <div>
 *          <button ng-click="showWithCloseAndAlert()">Show With Close And Alert</button>
 *          <button ng-click="withOutCloseAndAlert()">with Out Close And Alert</button>
 *        </div>
 *    </div>
 *   </file>
 *   <file name="script.js">
 *     angular.module('easeErrorMessageExample', ['easeErrorMessage'])
 *    .controller('errorController', function($scope) {
 *
 *       $scope.withOutClose = {
 *        closeAlert: true,
 *        iconColor: '#ff0000',
 *        isCloseIcon: false,
 *        alertIcon: true,
 *        globalElementId: 'hello',
 *        alertContent: 'easeErrorMessage component with alert icon',
 *        alertIconAria: 'Alert Icon'
 *       };
 *
 *       $scope.withClose= {
 *        closeAlert: true,
 *        iconColor: '#ff0000',
 *        isCloseIcon: true,
 *        globalElementId: 'hello',
 *        alertContent: 'easeErrorMessage component with \'X\'',
 *        closeIconAria: 'Close Icon'
 *       }
 *
 *       $scope.withAlertAndClose = {
 *        closeAlert: false,
 *        iconColor: '#ff0000',
 *        isCloseIcon: true,
 *        alertIcon: true,
 *        globalElementId: 'hello',
 *        alertContent: 'easeErrorMessage component with alert icon and \'X\'',
 *        closeIconAria: 'Close Icon',
 *        alertIconAria: 'Alert Icon'
 *       };
 *
 *       $scope.withOutAlertClose = {
 *        closeAlert: false,
 *        iconColor: '#ff0000',
 *        isCloseIcon: false,
 *        alertIcon: false,
 *        globalElementId: 'hello',
 *        alertContent: 'easeErrorMessage component without alert icon and \'X\''
 *       };
 *
 *
 *
 *        $scope.showWithCloseAndAlert = function () {
 *          $scope.withAlertAndClose.closeAlert = true
 *        };
 *
 *        $scope.withOutCloseAndAlert = function () {
 *          $scope.withOutAlertClose.closeAlert = true
 *        };
 *
 *        $scope.callback= function(options) {
 *          var color = "#";
 *           for (k = 0; k < 3; k++) {
 *             color += ("0" + (Math.random()*256|0).toString(16)).substr(-2);
 *            }
 *            $scope.withAlertAndClose.iconColor = color;
 *        }
 *     })
 *   </file>
 * </example>
 */
export default angular.module('easeErrorMessage', [])
    .component('easeErrorMessage', easeErrorMessageComponent);



