import * as angular from 'angular';
import easeCalendarComponent from './easeCalendar.component';
import easeCalendarConfig from './easeCalendar.multiDate';
import { easeCalendarPopup, easeCalendarPopupWrap } from './easeCalendarPopup.component';
import multiClass from '../../utilities/multiClass.util';
/**
 * @ngdoc module
 * @name easeCalendarPopup
 * @module easeCalendarPopup
 *
 * @description
 * This component displays calendar in the form of popup/flyover/dropdown. Add 'ease-calendar-popup' as an attribute
 * on your HTML element where you want to append the calendar as popup. This component accepts the same object
 * json as inline calendar except the addition of one extra property. Please follow Calendar documentation for more
 * robust details.
 *
 * @usage
 * ```html
 * <input
 *  ease-calendar-popup
 *  ng-model="vm.dt"
 *  is-open = "vm.isOpen"
 *  calendar-options="vm.inlineOptions">
 * ```
 *
 * @param {expression} is-open false to hide / true to display the popup. This attribute needs to be on your element.
 * @param {object} calendar-object exactly similar to inline calendar except one additional
 * property `placement` that gives you ability to specify where you want to place the popup.
 * Possible values for placement could be
 * 'top-center', 'top-left', 'top-right', 'bottom-center', 'bottom-left', 'bottom-right'. Adding 'auto',
 * like `auto top-center` would delegate smart positioning meaning popup/calendar will be positioned based on available
 * dimensions.
 * @param {Number=} gutter This property is also part of calendar object. Give appropiate value based on your
 * base element height. This only used in case you want to align the popup with your input field.
 *
 * @example
 * <example name="ease-calendar-popup" module="easeCalendarPopupExample">
 *   <file name="easeCalendarPopupExample.js">
 *       angular.module('easeCalendarPopupExample', ['easeUIComponents'])
 *         .controller('CalendarDemoCtrl', function($scope) {
 *           var self = this;
 *           this.dt = new Date();
 *           moment.locale('en');
 *           this.isOpen = false;
 *           this.open = function() {
 *            this.isOpen = true;
 *           };
 *           this.inlineOptions = {
 *             format_day_title: 'MMMM YYYY',
 *             placement: 'bottom-center',
 *             calendar_format: 'MMM, DD YYYY',
 *             min_date: new Date('01/01/2016'),
 *             max_date: new Date('12/01/2016'),
 *             customClass: function (data) {
 *              var myDueDate = new Date('September 14, 2016').setHours(0,0,0,0);
 *              if(data.date == myDueDate) {
 *                return {cssClass: 'due-day', subLabel: 'DUE'};
 *              } else {
 *                return {cssClass: '', subLabel: ''};
 *              }
 *             }
 *           };
 *         })
 *   </file>
 *   <file name="style.css">
 *       .date-input {
 *        height: 40px;
 *        width: 500px;
 *        margin: 50px;
 *       }
 *   </file>
 *   <file name="index.html">
 *     <p>Input tag is not implemented as a part of ease-calendar-popup here. This is consumers responsibility <br />
 *        to provide or implement input field with date validation.
 *     </p>
 *     <div ng-controller="CalendarDemoCtrl as vm2">
 *       <pre>Date from popup : <em>{{vm2.dt | date:'fullDate' }}</em> </pre>
 *       <input
 *          class = "date-input"
 *          type="text" ease-calendar-popup
 *          ng-model="vm2.dt"
 *          is-open = "vm2.isOpen"
 *          calendar-options = "vm2.inlineOptions"
 *          ng-click= "vm2.open()"
 *        />
 *     </div>
 *   </file>
 * </example>
 */
export default angular.module('easeCalendar', [])
    .config(['$provide', '$injector', easeCalendarConfig])
    .directive('multiClass', multiClass)
    .directive('easeCalendar', [easeCalendarComponent])
    .directive('easeCalendarPopup', [easeCalendarPopup])
    .directive('easeCalendarPopupWrap', easeCalendarPopupWrap);



