import Controller from './easeTooltip.controller';
import 'ease-tooltip.scss';
let easeTooltipComponent = {
    restrict: 'E',
    bindings: {
        options: '=?',
        tipMessage: '='
    },
    controller: ['$element', '$scope', '$compile', '$window',
        function ($element, $scope, $compile, $window) {
            let controller = new Controller($element, this.tipMessage, $scope.$id, this.options, $window);
            // controller does render
            $compile($element.contents())($scope.$parent);
            return controller;
        }]
};
export default easeTooltipComponent;



