import * as angular from 'angular';
import easeUiDropMenuComponent from './easeUiDropdownMenu.component';
/**
 * @ngdoc module
 * @name easeUiDropdownmenu
 * @module easeUiDropdownmenu
 *
 * @description
 * This component creates the dropdown menu. It takes in array of Items and renders each item in new line. It supports custom template if multiple column
 * in a single line. It expects the callback function to return the selcted item. It is fully accessibility with navigation keys up, down, tab, esc and return key. If you are providing your template don't forget to provide data-index = {{$index}}. Data-index is used to track click event index.
 *
 * @usage
 * ```html
 * <ease-ui-dropdownmenu
 *        items = "vm.items"
 *        select = "vm.selected(item)"
 *        template = true
 *        is-open = "!vm.isOpen"
 *        component-id = "build-book-id"
 *        text-overflow-ellipsis = true
 *        >
 *        <li data-ng-repeat = "item in vm.items"
 *          data-index = {{$index}} //required
 *          role = "option"
 *          tabindex = "-1"
 *        >
 *            <span class="ease-itemlabel">{{item.label}}</span>
 *            <span class="ease-itemSubLabel">{{item.subLabel}}</span>
 *            <span class="ease-item-amount">{{item.amount}}</span>
 *        </li>
 * </ease-ui-dropdownmenu>
 * ```
 *
 * @param {Array} items List of objects that holds items that you want to display in the dropdown menu.
 * @param {Function} select callback function you invoke once user select an item from the dropdown menu.
 * @param {Boolean} template true if provided with ng-transclude value else false.
 * @param {Boolean} is-open This hide/show dropdown.
 * @param {String} component-id This is a id attribute value for the line tags.
 * @param {Boolean=} text-overflow-ellipsis This will provide fexiblity for consumer to display ellipsis on primary data-point.
 *
 * @example
 * <example name="easeDropDownMenu" module="easeDropDownMenu">
 *   <file name="index.html">
 *    <h1>Dropdown-menu</h1>
 *    <h4>Multiple data-point menu. String truncation clipped overflow.</h4>
 *    <div ng-controller="testCtrl as vm">
 *      <button data-ng-click="vm.isOpen = !vm.isOpen">Open</button>
 *      <pre>Selected Item: {{vm.selectedItem}}</pre>
 *      <ease-ui-dropdown-menu
 *          select="vm.selected(item)"
 *          items="vm.items"
 *          template = true
 *          is-open = "vm.isOpen"
 *          component-id = "payment"
 *      >
 *        <li data-ng-repeat = "item in vm.items"
 *          data-index = {{$index}}
 *          role = "option"
 *          tabindex="-1"
 *        >
 *            <span class="ease-itemlabel">{{item.label}}</span>
 *            <span class="ease-itemSubLabel">{{item.subLabel}}</span>
 *            <span class="ease-item-amount">{{item.amount}}</span>
 *        </li>
 *      </ease-ui-dropdown-menu>
 *    </div>
 *    <h4>Multiple data point dropdown-menu with String truncation ellipsis overflow for mobile devices.</h4>
 *    <div ng-controller="testCtrlNoTextOverFlow as vm">
 *      <button data-ng-click="vm.isOpen = !vm.isOpen">Cards</button>
 *      <pre>Selected Item: {{vm.selectedItem}}</pre>
 *      <ease-ui-dropdown-menu
 *          select="vm.selected(item)"
 *          items="vm.items"
 *          template = true
 *          is-open = "vm.isOpen"
 *          component-id = "payment"
 *          text-overflow-ellipsis = true
 *      >
 *        <li data-ng-repeat = "item in vm.items"
 *          data-index = {{$index}}
 *          role = "option"
 *          tabindex="-1"
 *        >
 *            <span class="ease-itemlabel">{{item.label}}</span>
 *            <span class="ease-itemSubLabel">{{item.subLabel}}</span>
 *            <span class="ease-item-amount">{{item.amount}}</span>
 *        </li>
 *      </ease-ui-dropdown-menu>
 *    </div>
 *    <h4>Multiple data-point menu. String truncation with word clip. </h4>
 *    <div ng-controller="testCtrlWithEllipsis as vm">
 *    <button data-ng-click="vm.isOpen = !vm.isOpen">Open</button>
 *    <pre>Selected Item: {{vm.selectedItem}}</pre>
 *    <ease-ui-dropdown-menu
 *        select="vm.selected(item)"
 *        items="vm.items"
 *        template = true
 *        is-open = "vm.isOpen"
 *        component-id = "payment"
 *        text-overflow-ellipsis = false
 *    >
 *      <li data-ng-repeat = "item in vm.items"
 *        data-index = {{$index}}
 *        role = "option"
 *        tabindex="-1"
 *      >
 *          <span class="ease-itemlabel">{{item.label}}</span>
 *          <span class="ease-itemSubLabel">{{item.subLabel}}</span>
 *          <span class="ease-item-amount">{{item.amount}}</span>
 *      </li>
 *    </ease-ui-dropdown-menu>
 *    </div>
 *    <h4>Single data-point menu with anchor tag</h4>
 *    <div ng-controller="testCtrl1 as vm">
 *    <button data-ng-click="vm.isOpen = !vm.isOpen">Profile setting</button>
 *    <pre>Selected Item: {{vm.selectedItem}}</pre>
 *    <ease-ui-dropdown-menu
 *        select="vm.selected(item)"
 *        items="vm.items"
 *        template = true
 *        is-open = "vm.isOpen"
 *        component-id = "profile"
 *        text-overflow-ellipsis = true
 *    >
 *      <li data-ng-repeat = "item in vm.items"
 *        data-index = {{$index}}
 *        role = "option"
 *        tabindex="-1"
 *      >
 *          <a class="ease-itemlabel" ng-href={{item.link}} data-ui-sref={{item.link}} target="_blank">{{item.label}}</a>
 *      </li>
 *    </ease-ui-dropdown-menu>
 *    </div>
 *    <div ng-controller="testCtrlNoTemplate as vm">
 *    <h4>Standard Dropdown Menu (No template)</h4>
 *    <button data-ng-click="vm.isOpen = !vm.isOpen">State</button>
 *    <pre>Selected Item: {{vm.selectedItem}}</pre>
 *      <ease-ui-dropdown-menu
 *          select="vm.selected(item)"
 *          items="vm.items"
 *          is-open = "vm.isOpen"
 *          component-id = "basic"
 *      >
 *      </ease-ui-dropdown-menu>
 *    </div>
 *
 *   </file>
 *   <file name="script.js">
 *       angular.module('easeDropDownMenu',['easeUIComponents'])
 *                .controller('testCtrl',function(){
 *                 this.isOpen = false
 *                 this.selectedItem = ''
 *
 *                 this.items = [
 *                   {label:'Credit Cards this is very long name Credit Cards this is very long name', subLabel:'...1234', amount:'$300.00'},
 *                   {label:'Debit Cards', subLabel:'..2345', amount:'$200.00'},
 *                   {label:'Home Loans', subLabel:'..5656', amount:'$20.00'},
 *                   {label:'Gift Card', subLabel:'', amount:'', link:'/logout'}
 *                 ]
 *                 this.selected = function(item){
 *                   this.selectedItem = item
 *                 }
 *               })
 *                .controller('testCtrlNoTextOverFlow',function(){
 *                 this.isOpen = false
 *                 this.selectedItem = ''
 *                 this.items = [
 *                   {label:'Credit Cards this is very long name Credit Cards this is very long name', subLabel:'...1234', amount:'$300.00'},
 *                   {label:'Debit Cards', subLabel:'..2345', amount:'$200.00'},
 *                   {label:'Home Loans', subLabel:'..5656', amount:'$20.00'},
 *                   {label:'Gift Card', subLabel:'', amount:'', link:'/logout'}
 *                 ]
 *                 this.selected = function(item){
 *                   this.selectedItem = item
 *                 }
 *               })
 *               .controller('testCtrlWithEllipsis',function(){
 *                 this.isOpen = false
 *                 this.selectedItem = ''
 *
 *                 this.items = [
 *                   {label:'Credit Cards this is very long name Credit. Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum.', subLabel:'...1234', amount:'$300.00'},
 *                   {label:'Debit Cards', subLabel:'..2345', amount:'$200.00'},
 *                   {label:'Home Loans', subLabel:'..5656', amount:'$20.00'},
 *                   {label:'Gift Card', subLabel:'', amount:'', link:'/logout'}
 *                 ]
 *                 this.selected = function(item){
 *                   this.selectedItem = item
 *                 }
 *               })
 *                .controller('testCtrl1',function(){
 *                 this.isOpen = false
 *                 this.selectedItem = ''
 *
 *                 this.items = [
 *                   {label:'Personal Information', link:'https://www.capitalone.com/'},
 *                   {label:'Security Setting', link:'https://www.capitalone.com/'},
 *                   {label:'Account Preferences', link:'https://www.capitalone.com/'},
 *                   {label:'Sign Out', link:'https://www.capitalone.com/'}
 *                 ]
 *                 this.selected = function(item){
 *                   this.selectedItem = item
 *                 }
 *               })
 *               .controller('testCtrlNoTemplate',function(){
 *                 this.isOpen = false
 *                 this.selectedItem = ''
 *                 this.items = ['State name that has very long name is here. if there are more characters they will be clipped.', 'Virgina', 'Texas','North Carolina']
 *                 this.selected = function(item){
 *                   this.selectedItem = item
 *                 }
 *               })
 *
 *   </file>
 * </example>
 */
export default angular.module('easeUiDropdownMenu', [])
    .component('easeUiDropdownMenu', easeUiDropMenuComponent);



