import controller from './easeProfilePicture.controller';
import 'ease-profile-picture.scss';
const easeProfilePictureComponent = {
    bindings: {
        newImageData: '=',
        currentImageData: '=',
        loadingClass: '='
    },
    template: `<div role='button' id = "resultImg" tabindex = "0" class="dropzone" data-ng-class = "vm.hasImg() ? 'withimage' : 'noimage'" data-ng-click = "vm.uploadImage()" aria-label="{{vm.ariaMsg()}}">
      <input type="file" aria-label="{{vm.ariaMsg()}}" id="cropImgFile" accept="image/jpeg, image/png, image/jpg, image/gif"/>
      <img alt = "Profile Picture"  ng-src="{{vm.currentImageData}}"/>
   </div>`,
    controller,
    controllerAs: 'vm'
};
export default easeProfilePictureComponent;



