        let resizeRatio = { [KEY.UP]: 1.01, [KEY.RIGHT]: 1.01, [KEY.DOWN]: 0.99, [KEY.LEFT]: 0.99 };
        let moveRatio = { [KEY.UP]: [1, 0.99], [KEY.RIGHT]: [1.01, 1], [KEY.DOWN]: [1, 1.01], [KEY.LEFT]: [0.99, 1] };
