class easeErrorMessageController {
    /* @ngInject */
    constructor($scope, $element) {
        this._$scope = $scope;
        this._$element = $element;
        this.init(this.errorOptions);
    }
    // Initialize the data with defaults
    init(errorModel) {
        let { closeAlert, isCloseIcon, iconColor, alertIcon, alertContent, alertIconAria, closeIconAria, globalElementId } = errorModel;
        const errorMessageData = {
            closeAlert,
            iconColor,
            isCloseIcon,
            alertIcon,
            alertContent,
            alertIconAria,
            closeIconAria,
            globalElementId
        };
    }
    // close the error message alert
    closeErrorMessage() {
        this.errorOptions.closeAlert = false;
        if (this.callback)
            this.callback(this.errorOptions);
    }
}
export default easeErrorMessageController;



