import { KEY } from '../../../utilities/keyCode.util';
import { PROPERTIES } from '../../../utilities/componentProperties.util';
export default (ctrls, scope, element) => {
    let keyCode, cursorPos;
    ctrls.staticContent.placeholder = ctrls.staticContent.placeholder || ctrls.placeholder || PROPERTIES.forminputs.phone.hint;
    ctrls.staticContent.validationMsg = ctrls.staticContent.validationMsg || ctrls.validationMsg || PROPERTIES.forminputs.phone.error;
    ctrls.charLimit = 14;
    ctrls.type = 'tel';
    ctrls.validateInput = (input) => {
        let pattern = /^\([0-9]{3}\)\s{1}[0-9]{3}\-[0-9]{4}$/;
        let inputResult = input.content.trim();
        return pattern.test(inputResult);
    };
    ctrls.onInput = () => {
        ctrls.hasError = false;
        ctrls.errorOutput = '';
        ctrls.inputChanged = true;
        ctrls.timeout(() => {
            ctrls.formatPhone();
            let content = ctrls.inputContent.value;
            ctrls.contentLength = content.length;
        });
    };
    ctrls.onFocus = () => {
        ctrls.inFocus = false;
        ctrls.isBlur = false;
        ctrls.inputChanged = false;
        ctrls.formatPhone();
    };
    ctrls.onBlur = (e) => {
        ctrls.isBlur = true;
        ctrls.inputVal = ctrls.inputContent.value;
        ctrls.checkContent(e);
    };
    ctrls.onKeydown = (e) => {
        keyCode = e.which;
        keyCode === KEY.SPACE ? e.preventDefault() : null;
        ctrls.handleKeydown(e);
    };
    ctrls.formatPhone = () => {
        let isDeleteKey = keyCode === KEY.BACKSPACE || keyCode === KEY.DELETE;
        cursorPos = ctrls.inputContent.selectionStart || 14;
        isDeleteKey && cursorPos === 14 ? cursorPos = 1 : null;
        let numbers = ctrls.inputContent.value.replace(/\D/g, '');
        let formatChars = { 2: ') ', 5: '-' };
        // move the cursor for the '( ' and '-''
        let cursorMoves = { 1: 2, 4: 6, 5: 7, 6: 7, 9: 10, 10: 11 };
        // re-position the cursor when deleting in the input box
        let cursorBackspaceMoves = { 4: 3, 5: 3, 9: 8 };
        // delete the number when deleting ( and -
        let numberBackSpaceDeleted = { 4: 2, 5: 2, 9: 5 };
        let numberDeleted = { 4: 3, 5: 3, 9: 6 };
        let phoneNum = '';
        if (keyCode === KEY.BACKSPACE && numberBackSpaceDeleted[cursorPos]) {
            numbers = numbers.substr(0, numberBackSpaceDeleted[cursorPos]) + numbers.substr(numberBackSpaceDeleted[cursorPos] + 1);
            cursorPos = cursorBackspaceMoves[cursorPos];
        }
        if (keyCode === KEY.DELETE && numberDeleted[cursorPos]) {
            numbers = numbers.substr(0, numberDeleted[cursorPos]) + numbers.substr(numberDeleted[cursorPos] + 1);
        }
        for (let i = 0; i < numbers.length; i++) {
            i === 0 ? phoneNum = '(' : null;
            phoneNum += numbers[i] + (formatChars[i] || '');
        }
        ctrls.inputContent.value = phoneNum;
        if (!isDeleteKey) {
            cursorPos = cursorMoves[cursorPos] || cursorPos;
        }
        ctrls.inputVal = ctrls.inputContent.value;
        keyCode = '';
        ctrls.timeout(() => ctrls.inputContent.setSelectionRange(cursorPos, cursorPos));
    };
};



