class CarouselController {
    prev() {
        if (this.havePrev()) {
            this.togglePrev();
        }
    }
    next() {
        if (this.haveNext()) {
            this.toggleNext();
        }
    }
    prevExists() {
        return this.havePrev();
    }
    nextExists() {
        return this.haveNext();
    }
    isAvailable(dir) {
        if (dir === 'prev') {
            return this.havePrev() ? 'available' : 'not available';
        }
        else {
            return this.haveNext() ? 'available' : 'not available';
        }
    }
}
export default CarouselController;



