import controller from './easeMonthPicker.controller';
import 'ease-month-picker.scss';
export default {
    restrict: 'E',
    bindings: {
        months: '=',
        currentMonth: '='
    },
    template: `
    <div class="ease-month-picker">
      <ul class="ease-month-display">
        <li class="ease-month" data-ng-repeat="month in vm.months" data-ng-class="{'disabled': !month.available}">
          <button class="ease-month-bubble" data-ng-class="{'active': vm.isActive(month, vm.currentMonth), 'disabled': !month.available, 'noDate': !month.date}" aria-label="{{month.month}}, {{vm.isAvailable(month.available)}}" id="{{month.month}}-bubble" data-ng-click="vm.selectMonth(month)">
            <b>{{month.month}}</b><br>{{month.date}}
          </button>
        </li>
      </ul>
    </div>
  `,
    controller,
    controllerAs: 'vm'
};



