import * as angular from 'angular';
import easeQuarterlyPickerComponent from './easeQuarterlyPicker.component';
/**
  * @ngdoc module
  * @name easeQuarterlyPicker
  * @module easeQuarterlyPicker
  *
  * @description
  * Component that takes in an array of quarter year objects and outputs it as a grid where the user can select one.  Only quarters
  * that are available will be clickable for the user, any unavailable quarters will not be visible.  The component stores the
  * selected quarter in a variable (currentQuarter) that can be accessed and used.
  *
  * @usage
  * ```html
  *<ease-quarterly-picker quarters="listOfQuarters" current-quarter="someQuarter"></ease-quarterly-picker>
  * ```
  *
  * @param {array} quarters List of quarter objects that include name of quarter (title) and if quarter is available, stored as true/false (available).
  * @param {object} currentQuarter Variable that will be used to track the quarter selected by the user.
  * @example
  * <example name="quarterly-picker" module="quarterlyPickerExample">
  *   <file name="index.html">
  *     <script src="script.js"></script>
  *     <div ng-controller="QuarterlyPickerController" style="width: 240px; margin: 0 auto;">
  *       <ease-quarterly-picker quarters="quarterList" current-quarter="selectedQuarter"></ease-quarterly-picker>
  *       <p> Selected quarter is {{selectedQuarter}} </p>
  *       <button ng-click="changeQuarterly(1)">Click to see one quarterly statement</button><br>
  *       <button ng-click="changeQuarterly(2)">Click to see two quarterly statements</button><br>
  *       <button ng-click="changeQuarterly(3)">Click to see three quarterly statements</button><br>
  *       <button ng-click="changeQuarterly(4)">Click to see four quarterly statements</button>
  *     </div>
  *   </file>
  *   <file name="script.js">
  *     angular.module('quarterlyPickerExample', ['easeUIComponents'])
  *       .controller('QuarterlyPickerController', ['$scope', function($scope) {
  *         $scope.quarterList = [{title: 'Jan-MarQ1 Summary', available: true}, {title: 'Apr-JunQ2 Summary', available: true},
  *           {title: 'Jul-SepQ3 Summary', available: true}, {title: 'Oct-DecQ4 Summary', available: true}]
  *         $scope.selectedQuarter = {}
  *         var q1 = $scope.quarterList.slice(0, 1)
  *         var q2 = $scope.quarterList.slice(0, 2)
  *         var q3 = $scope.quarterList.slice(0, 3)
  *         var q4 = $scope.quarterList.slice(0)
  *
  *         $scope.changeQuarterly = function(num) {
  *           if (num === 1) {
  *             $scope.quarterList = q1
  *           } else if (num === 2) {
  *             $scope.quarterList = q2
  *           } else if (num === 3) {
  *             $scope.quarterList = q3
  *           } else {
  *             $scope.quarterList = q4
  *           }
  *           $scope.selectedQuarter = {}
  *         }
  *       }])
  *   </file>
  * </example>
*/
export default angular.module('easeQuarterlyPicker', [])
    .component('easeQuarterlyPicker', easeQuarterlyPickerComponent);



