import * as Mustache from 'mustache';
import * as angular from 'angular';
import { getDataModel } from '../../utilities/dataModel.util';
import partial from './easeTxnTable.mustache';
import columnHeadersPartial from './partials/easeTxnTableColumnHeaders.mustache';
import columnHeaderPartial from './partials/easeTxnTableColumnHeader.mustache';
import rowsPartial from './partials/easeTxnTableRows.mustache';
import cellPartial from './partials/easeTxnTableCell.mustache';
import noResultsPartial from './partials/easeTxnTableNoResults.mustache';
import { PROPERTIES } from '../../utilities/componentProperties.util';
const moment = require('moment');
const _ = require('lodash');
const COL_GROUPS = ['leftDataPoints', 'middleDataPoints', 'rightDataPoints'];
const DATA_POINTS = ['primary', 'secondary', 'tertiary', 'quaternary'];
const updateColumnHeaderId = ({ id, columns }) => {
    let update = (column) => {
        if (column) {
            column.id = id.concat('-col-', column.id);
        }
    };
    _.forEach(COL_GROUPS, (group) => {
        _.forEach(DATA_POINTS, (dataPoint) => {
            update(columns[group][dataPoint]);
        });
    });
};
const addSecondaryDataToPrimary = (dataPoint, primary, cellData) => {
    if (primary) {
        primary.secondaryData = cellData;
        if (primary.supplement) {
            primary.delimiter = '-';
        }
    }
};
const execFunction = (controller, functionName, transaction) => {
    if (functionName) {
        let func = controller[functionName];
        if (func) {
            return func(transaction);
        }
    }
};
const buildCellValue = (columnConfig, transaction, controller) => {
    let value;
    if (columnConfig.field) {
        value = transaction[columnConfig.field];
    }
    else if (columnConfig.valueFunction) {
        value = execFunction(controller, columnConfig.valueFunction, transaction);
    }
    return value;
};
const buildRow = ({ id: tableId, columns: columnConfigs }, transaction, controller) => {
    let buildCellData = (group, dataPoint, columnConfig) => {
        if (!columnConfig) {
            return;
        }
        let cellData = { id: tableId.concat('-', columnConfig.id, '-', transaction.index) };
        if (columnConfig.customClassFunction) {
            let customClass = execFunction(controller, columnConfig.customClassFunction, transaction);
            if (customClass) {
                cellData.cellClass = customClass;
            }
        }
        cellData.value = buildCellValue(columnConfig, transaction, controller);
        if (cellData.value instanceof Date) {
            cellData.isDate = true;
            cellData.value = moment(cellData.value).format('YYYY-MM-DD');
        }
        if (columnConfig.isLogo) {
            cellData.isLogo = columnConfig.isLogo;
        }
        if (columnConfig.supplement) {
            let value = buildCellValue(columnConfig.supplement, transaction, controller);
            if (value) {
                cellData.supplement = {
                    id: tableId.concat('-', columnConfig.id, '-', columnConfig.supplement.id, '-', transaction.index),
                    value: value
                };
            }
        }
        cellData.transactionIndex = transaction.index;
        if (columnConfig.columnLabel) {
            cellData.label = columnConfig.columnLabel;
        }
        if (columnConfig.actions) {
            cellData.actions = _.filter(columnConfig.actions, (action) => {
                return execFunction(controller, action.condition, transaction);
            }).map((action, index) => {
                return {
                    actionLabel: action.label,
                    actionOnClick: action.onclick,
                    actionDelimiter: index === 0,
                    actionClass: action.label.toLowerCase()
                };
            });
            cellData.haveActions = cellData.actions.length > 0;
        }
        if (!cellData.isDate && !cellData.isLogo) {
            cellData.isString = true;
        }
        return cellData;
    };
    let row = {
        cells: {},
        transaction,
        id: tableId.concat('-txn-', transaction.index)
    };
    _.forEach(COL_GROUPS, group => {
        let cellsGroup = row.cells[group] = {};
        _.forEach(DATA_POINTS, dataPoint => {
            let cellData = buildCellData(group, dataPoint, columnConfigs[group][dataPoint]);
            if (cellData) {
                cellsGroup[dataPoint] = cellData;
                if (dataPoint === 'secondary') {
                    addSecondaryDataToPrimary(dataPoint, cellsGroup['primary'], cellData);
                }
            }
        });
    });
    return row;
};
const buildRows = ({ dataModel, controller }) => {
    return _.map(dataModel.transactions, transaction => {
        return buildRow(dataModel, transaction, controller);
    });
};
const render = (element, scope, compile, html) => {
    element.html(html);
    compile(element.contents())(scope);
};
class EaseTxnTableController {
    /* @ngInject */
    constructor($element, $scope, $compile, $templateRequest) {
        this.element = $element;
        this.scope = $scope;
        this.compile = $compile;
        this.templateRequest = $templateRequest;
        if (_.isEmpty(this.content)) {
            this.error = 'Content is null or undefined';
            return;
        }
        if (_.isEmpty(this.controller)) {
            this.error = 'Controller is null or undefined';
            return;
        }
        this.staticContent = this.staticContent || {};
        this.staticContent.noTransactions = this.staticContent.noTransactions || PROPERTIES.transtable.notransactions;
        this.scope.$watch(() => { return this.content.transactions; }, () => { this.setupTable(); });
    }
    setupTable() {
        this.dataModel = getDataModel(this.content);
        this.dataModel.arialabelById = this.arialabelById;
        this.dataModel.noTransactions = this.staticContent.noTransactions;
        // drawer-state-change
        document.addEventListener('drawer-state-change', (e) => {
            let drawerState = { open: true, close: false };
            let drawerId = (e.detail.drawerId).replace('-drawer', '');
            let drawerContainer = angular.element(document.getElementById(drawerId));
            let isExpanded = drawerState[e.detail.actionType];
            drawerContainer.attr('aria-expanded', isExpanded);
            this.resizeLogo(drawerId, drawerState[e.detail.actionType]);
        });
        if (this.dataModel.transactions && this.dataModel.transactions.length > 0) {
            this.dataModel.transactions = this.dataModel.transactions.map((transaction, index) => {
                return Object.assign(transaction, { index });
            });
            if (this.dataModel.drawerTemplateUrl) {
                this.templateRequest(this.dataModel.drawerTemplateUrl).then((drawerPartial) => {
                    Object.assign(this.dataModel, { drawerPartial });
                    render(this.element, this.scope, this.compile, this.buildHtml(true));
                });
            }
            else {
                render(this.element, this.scope, this.compile, this.buildHtml(true));
            }
            this.dataModel.rows = buildRows(this);
            updateColumnHeaderId(this.dataModel);
        }
        else {
            render(this.element, this.scope, this.compile, this.buildHtml(false));
        }
    }
    buildHtml(haveTransactions) {
        if (this.error) {
            this.html = this.error;
        }
        else {
            let partials = haveTransactions ? { columnHeadersPartial, columnHeaderPartial, rowsPartial, cellPartial } : { noResultsPartial };
            if (this.dataModel.drawerPartial) {
                partials.drawerPartial = this.dataModel.drawerPartial;
            }
            this.html = Mustache.render(partial, this.dataModel, partials);
        }
        return this.html;
    }
    handleClick(index, functionName) {
        execFunction(this.controller, functionName, this.dataModel.transactions[index]);
    }
    resizeLogo(drawerId, drawerState) {
        let imageContainer = angular.element(document.getElementById(drawerId).getElementsByTagName('img')).parent();
        if (drawerState) {
            imageContainer.addClass('ease-txn-logo-grow');
        }
        else {
            imageContainer.removeClass('ease-txn-logo-grow');
        }
    }
}
export default EaseTxnTableController;



