import { KEY } from './keyCode.util';
export const focusDropdownEntry = (keyCode, elements, optionIndex) => {
    switch (keyCode) {
        case KEY.DOWN:
            if (optionIndex === null) {
                optionIndex = 0;
            }
            else {
                optionIndex = optionIndex === elements.length - 1
                    ? 0 : optionIndex + 1;
            }
            break;
        case KEY.UP:
            if (optionIndex === null) {
                optionIndex = elements.length - 1;
            }
            else {
                optionIndex = optionIndex === 0
                    ? elements.length - 1 : optionIndex - 1;
            }
            break;
    }
    removeClass(elements);
    elements.eq(optionIndex).addClass('active');
    setTimeout(() => {
        elements[optionIndex].focus();
    }, 0);
    return optionIndex;
};
let removeClass = elements => {
    for (let i = 0; i < elements.length; i++) {
        elements.eq(i).removeClass('active');
    }
};



